;(function ($, window, undefined) {
    'use strict';

    var pluginName = 'nav-tabs';

    function Plugin(element, options) {
        this.element = $(element);
        this.options = $.extend({}, $.fn[pluginName].defaults, this.element.data(), options);
        this.init();
    }

    Plugin.prototype = {
        init: function () {
            this.tabs();
            this.display();
        },

        display: function() {
            var el = this.element,
                ycLoader = el.prev('.yoga-challenge-loader'),
                tabLink  = el.find('.tab-link'),
                tabPane  = el.find('.tab-pane'),
                header   = el.find('.ywc-hero-image'),
                tabs     = el.find('.ywc-timeline');

            el.hide();

            this.setTabActive(tabLink);
            this.setTabActive(tabPane);

            ycLoader.hide();
            header.show();
            tabs.show();
            el.show();
        },

        setTabActive: function (tabEl) {
            var current  = new Date(),
                year     = current.getFullYear(),
                getMonth = (current.getMonth() + 1).toString(),
                month    = getMonth.length > 1 ? getMonth : '0' + getMonth, //adding leading 0
                getDay   = current.getDate().toString(),
                day      = getDay.length > 1 ? getDay : '0' + getDay, //adding leading 0
                currentDate = year + '-' + month + '-' + day,
                dateFlag = false;

            tabEl.removeClass('selected-day');

            tabEl.each(function (index, el) {
                var $tab    = $(el),
                    tabDate = $tab.data('tab-date');

                if ( tabDate === currentDate ) {
                    dateFlag = true;
                    $tab.addClass('selected-day');
                }
            });

            if ( dateFlag === false ) {
                tabEl.first().addClass('selected-day');
            }
        },

        tabs: function () {
            var el = this.element,
                tabLink = el.find('[data-tab]'),
                videoItem = $('.video-items');

            tabLink.on('click', function (ev) {
                ev.preventDefault();

                var me = $(this),
                    id = me.attr('data-tab');

                el.find('.tab-nav li, .tab-list .tab-pane').removeClass('selected-day');
                me.addClass('selected-day');
                el.find("#" + id).addClass('selected-day');

                videoItem.slick('setPosition', 0);
                videoItem.find('[data-textpanel]').textpanel('reInit');

                var img = $('.tab-pane#' + id).data('day-img');
                $('.hero-image').prop('src', img);

                var $maxHeight = 0,
                    $answerItems = $('.selected-day .question-answer li a');

                $answerItems.each(function (index, el) {
                    if ($(el).innerHeight() > $maxHeight) {
                        $maxHeight = $(el).innerHeight();
                    }
                });

                $answerItems.each(function (index, el) {
                    $(el).css('height', $maxHeight + 'px');
                });
            });
        },

        destroy: function () {
            $.removeData(this.element[0], pluginName);
        }
    };

    $.fn[pluginName] = function (options, params) {
        return this.each(function () {
            var instance = $.data(this, pluginName);
            if (!instance) {
                $.data(this, pluginName, new Plugin(this, options));
            } else if (instance[options]) {
                instance[options](params);
            }
        });
    };

    $.fn[pluginName].defaults = {
        key: 'value',
        onCallback: null
    };

    $(function () {
        var $pluginPanel = $('[data-' + pluginName + ']');

        $pluginPanel.on('customEvent', function () {
            // to do
        });

        $pluginPanel[pluginName]({
            key: 'custom'
        });
    });

}(jQuery, window));
