;(function ($, window, undefined) {
  'use strict';

  var pluginName = 'onboarding-process',
      classItemSelected = 'item-checked',
      classStepContent= '.step-content';

  function Plugin(element, options) {
      this.element = $(element);
      this.options = $.extend({}, $.fn[pluginName].defaults, this.element.data(), options);
      this.init();
  }

  Plugin.prototype = {
      init: function () {
        this.itemImage = this.element.find('[data-thumb-image]');

        this.handleEvent();
      },
      handleEvent: function () {
        var that = this;

        that.itemImage.on("click", function (e) {
          var $item = $(this),
              $currentCheckbox = $item.find('input[type="checkbox"]'),
              isChecked = $currentCheckbox.prop("checked"),
              uncheckedClass = 'unchecked';

          $item.toggleClass(classItemSelected).removeClass(uncheckedClass);
          $currentCheckbox.prop("checked",!isChecked);

          if (isChecked) {
            $item.addClass(uncheckedClass);
          }

          var $parentStep = $(this).closest(classStepContent),
              $inputHidden = $parentStep.find('input[type="hidden"]'),
              $listCheckbox = $parentStep.find('input[type="checkbox"]:checked');

          that.storeCheckedValue($listCheckbox, $inputHidden);
          e.preventDefault();
        });
      },
      storeCheckedValue: function (checkboxEle, inputHiddenEle) {
        var checkedValue = checkboxEle.map(function() {
          return this.value;
        }).get().join(',');

        inputHiddenEle.val(checkedValue);
      },
      destroy: function () {
          $.removeData(this.element[0], pluginName);
      }
  };

  $.fn[pluginName] = function (options, params) {
      return this.each(function () {
          var instance = $.data(this, pluginName);
          if (!instance) {
              $.data(this, pluginName, new Plugin(this, options));
          } else if (instance[options]) {
              instance[options](params);
          }
      });
  };

  $.fn[pluginName].defaults = {};

  $(function () {
      $('[data-' + pluginName + ']')[pluginName]();
  });

}(jQuery, window));
