;(function ($, window) {
  'use strict';

  var pluginName = 'menu-header',
    click = 'click.' + pluginName,
    documentClick = 'document-click.' + pluginName,
    classActive = 'active';

  function Plugin(element, options) {
      this.element = $(element);
      this.options = $.extend({}, $.fn[pluginName].defaults, this.element.data(), options);
      this.init();
  }

  Plugin.prototype = {
      init: function () {
        this.cacheDOM();
        this.handleEvent();
      },

      cacheDOM: function() {
        this.$doc = $(document);
        this.$btn = this.element.find('[data-menu-btn]');
        this.$navList = this.element.find('[data-menu-list]');
        this.$navbarAvatar = this.element.find('[data-navbar-avatar]');
        this.$actionList = this.element.find('[data-action-list]');
        this.$iconSearch = this.element.find('.global-search-icon');
        this.$iconCloseSearch = this.element.find('.close-global-search');
      },

      handleEvent: function() {
        var that = this;

        this.$btn
          .off(click)
          .on(click, function() {
            that.handleShowMenu();
          });

        this.$navbarAvatar
          .off(click)
          .on(click, function(e) {
            e.preventDefault();
            that.handleAction();
          });

        $(window)
          .on('breakpoint:change.' + pluginName, function() {
            that.hideMenu();
            that.hideAction();
          });

        this.$doc
          .off(documentClick)
          .on(documentClick, function(e, node) {
            that.clickOuside(node);
          });

        this.$iconSearch
          .off(click)
          .on(click, function () {
            var isHeaderMobile = $('.header-main-mobile').is(':visible'),
                $nav = $(this).closest('.navbar'),
                $overlaySearch = $nav.find('.overlay-search'),
                $header = $('header'),
                heightHeader = $header.height(),
                $subHeader = $header.find('.sub-header'),
                heightSubHead = typeof $subHeader !== 'undefined' && $subHeader.length ? $subHeader.height() : 0;

            if (isHeaderMobile) {
              that.handleShowMenu();
            }

            $overlaySearch.css({
              'height': heightHeader - heightSubHead,
              'top': heightSubHead
            }).toggleClass(classActive);
          });

        this.$iconCloseSearch
          .off(click)
          .on(click, function () {
            $(this).parents('.overlay-search').toggleClass(classActive);
            $(this).siblings('.overlay-content').find('input').val('');
          });
      },

      clickOuside: function(node) {
        var target = node.target,
          checkTarget = function ($main, $sub) {
            var condition = !$main.is(target) &&
              $main.has(target).length === 0 &&
              $main.hasClass(classActive) &&
              !$sub.is(target) &&
              $sub.has(target).length === 0 &&
              $sub.hasClass(classActive);

              return condition;
            };

        if (checkTarget(this.$navbarAvatar, this.$actionList)) {
          this.hideAction();
        }

        if (checkTarget(this.$btn, this.$navList)) {
          this.hideMenu();
        }
      },

      handleShowMenu: function() {
        var isBtnActive = this.$btn.hasClass(classActive);

        if(isBtnActive) {
          this.hideMenu();
        } else {
          this.showMenu();
        }
      },

      showMenu: function() {
        this.$btn.addClass(classActive);
        this.$navList.addClass(classActive);
      },

      hideMenu: function() {
        this.$btn.removeClass(classActive);
        this.$navList.removeClass(classActive);
      },

      handleAction: function() {
        var isActionActive = this.$navbarAvatar.hasClass(classActive);

        if (isActionActive) {
          this.hideAction();
        } else {
          this.showAction();
        }
      },

      showAction: function () {
        this.$navbarAvatar.addClass(classActive);
        this.$actionList.addClass(classActive);
      },

      hideAction: function() {
        this.$navbarAvatar.removeClass(classActive);
        this.$actionList.removeClass(classActive);
      },

      destroy: function () {
        $.removeData(this.element[0], pluginName);
      }
  };

  $.fn[pluginName] = function (options, params) {
      return this.each(function () {
          var instance = $.data(this, pluginName);
          if (!instance) {
              $.data(this, pluginName, new Plugin(this, options));
          } else if (instance[options]) {
              instance[options](params);
          }
      });
  };

  $.fn[pluginName].defaults = {};

  $(function () {
      $('[data-' + pluginName + ']')[pluginName]();
  });

}(jQuery, window));
