;(function ($, window) {
  'use strict';

  var pluginName = 'form-validate',
    regexName = /^(?!\s)(?!.*\s$)[a-zA-Z0-9\s()-]+$/,
    regexPassword = /^(?=.*[a-z])(?=.*[A-Z])(?=.*\d)[\w\W]{8,}$/,
    regexEmail  = /^([a-zA-Z0-9_.+-])+\@(?!-)+(([a-zA-Z0-9-])+[a-z0-9]\.)+([a-zA-Z0-9]{2,4})+$/,
    regexEmail2 = /^([a-zA-Z0-9_.-])+\@(?!-)+(([a-zA-Z0-9-])+[a-z0-9]\.)+([a-zA-Z0-9]{2,4})+$/,
    regexZip = /^[A-Za-z0-9 ]+$/,
    formGroupWrap = '.form-group-wrap',
    classValidError = 'validate-error',
    classDisabled = 'disabled',
    classHidden = 'd-none',
    classError = 'error',
    formatMMYY = 'MM/YY',
    formCallback = null,
    formElement = null,
    _cb = null,
    click = 'click.' + pluginName;


  function Plugin(element, options) {
    this.element = $(element);
    this.options = $.extend({}, $.fn[pluginName].defaults, this.element.data(), options);
    this.init();
  }

  Plugin.prototype = {
    init: function () {
      this.initValidate();
      this.cacheDOM();
      this.handleEvent();
    },

    initValidate: function () {
      var today = window.moment().format(formatMMYY);

      $.validator.addMethod("regexName", function (value, element) {
        return regexName.test(value);
      });

      $.validator.addMethod("regexPwd", function (value, element) {
        return regexPassword.test(value);
      });

      $.validator.addMethod("regexEmail", function (value, element) {
        return typeof yahAjax.allowPlus !== 'undefined' && yahAjax.allowPlus ? regexEmail.test(value) : regexEmail2.test(value);
      });

      $.validator.addMethod("regexZip", function (value, element) {
        return regexZip.test(value);
      });

      $.validator.addMethod("expiration", function (value, element) {
        var momentInput,
          momentToday,
          isValidDate = false;

        if (value.length > 4) {
          momentInput = window.moment(value, formatMMYY);
          momentToday = window.moment(today, formatMMYY);
          isValidDate = momentInput.diff(momentToday, 'months') > -1;
        }

        return isValidDate;
      });
    },

    cacheDOM: function () {
      this.$form = this.element;
      this.$btnSubmit = this.element.find('[data-btn-submit]');
      this.$btnGiftCode = this.element.find('[data-btn-gift-code]');
      this.$btnRedeemGiftCardCode = this.element.find('[data-btn-redeem-gift-card-code]');
      this.$onboardingStart = this.element.find('[data-btn-onboarding-start]');
      this.$onboardingCheckout = this.element.find('[data-btn-onboarding-checkout]');
      this.$btnUpdatePassword = this.element.find('[data-btn-update-password]');
      this.$btnCouponCode = this.element.find('[data-btn-coupon]');
      this.$btnSettingsPaymentMethod = this.element.find('[data-btn-settings-payment-method]');
      this.$btnSettingsGiftCardCode = this.element.find('[data-btn-settings-redeem-gift-card-code]');
      this.$btnSettingsRedeemCode = this.element.find('[data-btn-settings-redeem-code]');
      this.$btnCheckoutRedeemCode = this.element.find('[data-btn-checkout-redeem-code]');
      this.$btnUpdatePlan = this.element.find('[data-btn-update-plan]');
      this.$btnUpdateOnetimePlan = this.element.find('[data-btn-update-onetime-plan]');
      this.$chbxIntlPayment = this.element.find('[data-intl-payment]');
      this.$btnTfaConfirm = this.element.find('[data-btn-tfa-confirm]');
      this.$btnTfaRequestCode = this.element.find('[data-btn-tfa-request-again]');
      this.$btnTfaUpdatePhone = this.element.find('[data-btn-tfa-update-phone-number]');
      this.$btnTfaSettingsConfirm = this.element.find('[data-btn-tfa-settings-confirm]');
      this.$btnTfaSettingsRequestCode = this.element.find('[data-btn-tfa-settings-request-again]');
    },

    handleEvent: function () {
      var that = this;

      this.$btnSubmit
        .off(click)
        .on(click, function () {
          that.handleValidate();
        });

      this.$onboardingStart
        .off(click)
        .on(click, function () {
          that.handleValidate(ywah_onboarding_helper.submit_onboarding_start);
        });

      this.$onboardingCheckout
        .off(click)
        .on(click, function () {
          var $target = $(this);

          that.element.find('.general-error').html('');

          that.handleValidate(function (element) {
            $target.prop('disabled', true).addClass(classDisabled);
            ywah_onboarding_helper.submit_onboarding_checkout(element, function () {
              $target.prop('disabled', false).removeClass(classDisabled);
            });
          });
        });

      this.$btnGiftCode
        .off(click)
        .on(click, function (e) {
          var $target = $(this);

          that.handleValidate(function(element) {
            $target.prop('disabled', true).addClass(classDisabled);
            ywah_onboarding_helper.submit_onboarding_gift_code(
              element,
              function (res) {that.redeemCouponCode(element, res); $target.prop('disabled', false).removeClass(classDisabled); },
              function() { $target.prop('disabled', false).removeClass(classDisabled); }
            );
          });
        });

      this.$btnRedeemGiftCardCode.off(click).on(click, function (e) {
        var $target = $(this),
          $checkoutForm = $('.ywah-onboarding-checkout'),
          $checkoutTitleWrap = $checkoutForm.find('.title-wrapper');

        that.handleValidate(function (element) {
          $target.prop('disabled', true).addClass(classDisabled);
          ywah_onboarding_helper.submit_onboarding_redeem_gift_card_code(
              element,
              function (res) {
                $('#giftCardCode').modal('hide');
                if (typeof helper_gateway.isNewSubscribeFlow !== 'undefined' && helper_gateway.isNewSubscribeFlow) {
                  $checkoutForm.find('input[name="gift_card_code"]').val(res.gift_code);
                  $checkoutForm.find('input[name="plan_code"]').val(res.plan_code);
                  $checkoutForm.find('.title-wrapper').html(res.cart_detail);
                  $checkoutForm.find('.wrap-apple').hide();
                  $checkoutForm.find('[data-apple-pay-wrap]').hide();
                  $checkoutForm.find('.onboarding-subscription').hide();
                  $('.onboarding-checkout-plan-list').hide();
                  $('.onboarding-checkout').show();
                } else {
                  $('[data-cart-detail-wrap]').html(res.cart_detail).show();
                  $checkoutForm.find('input[name="gift_card_code"]').val(res.gift_code);
                  $checkoutForm.find('input[name="plan_code"]').val(res.plan_code);
                  $('.checkout-form-promo input[name="plan_code"]').val(res.plan_code);
                  $target.prop('disabled', false).removeClass(classDisabled);
                  $('.apply-code span').trigger('click');

                  $('[data-promo-code-open-popup-btn]').removeClass('d-inline-block').addClass('d-none');
                  $('.onboarding-subscription-item').removeClass('active');
                  $('.onboarding-plan-item').removeClass('active').removeAttr('data-subscribe-tab').find('input').remove();
                  $('.onboarding-plan-primary .title-wrapper').hide();
                  $('.onboarding-plan-secondary').hide();
                  $('[data-onboarding-other-plan]').hide();
                  $('.onboarding-subscription-switch').hide();
                  $checkoutTitleWrap.find('[data-step-title]').html(res.plan_name);
                  $checkoutTitleWrap.find('[data-step-desc]').html(res.plan_desc);
                  $checkoutForm.find('.cc-form-inputs').hide();
                  $checkoutForm.find('.intl-payment-wrap').hide();
                  $('[data-apple-pay-wrap]').hide();
                  $('[data-btn-onboarding-checkout]').text('Start Now');
                }
              },
              function () {
                $target.prop('disabled', false).removeClass(classDisabled);
              }
          );
        });
      });

      this.$btnUpdatePassword
        .off(click)
        .on(click, function (e) {
          that.handleValidate(function (element) {
            that.handleUpdatePassword(element);
          });
        });

      this.$btnCouponCode
        .off(click)
        .on(click, function (e) {
          that.handleValidate(function (element) {
            that.handleCouponCode(element);
          });
        });

      this.$btnSettingsPaymentMethod
        .off(click)
        .on(click, function (e) {
          that.handleValidate(ywah_settings_payments_helper.submit_settings_payment_method);
        });

      this.$btnSettingsGiftCardCode.off(click).on(click, function (e) {
        that.handleValidate(ywah_settings_payments_helper.submit_settings_redeem_gift_card_code);
      });

      this.$btnSettingsRedeemCode
        .off(click)
        .on(click, function (e) {
          that.handleValidate(ywah_settings_payments_helper.submit_settings_redeem_code);
        });

      this.$btnCheckoutRedeemCode
        .off(click)
        .on(click, function (e) {
          var $target = $(this);

          that.handleValidate(function (element) {
            $target.prop('disabled', true).addClass(classDisabled);
            ywah_onboarding_helper.submit_onboarding_gift_code(
              element,
              function (res) {
                $('[data-account-plan-management]')['account-plan-management']('applyPromoCodeEvent', res);
                $target.prop('disabled', false).removeClass(classDisabled);
              },
              function () {
                $target.prop('disabled', false).removeClass(classDisabled);
              }
            );
          });
        });

      this.$btnUpdatePlan
        .off(click)
        .on(click, function (e) {
          that.handleValidate(ywah_settings_payments_helper.submit_update_plan);
        });

      this.$btnUpdateOnetimePlan
        .off(click)
        .on(click, function (e) {
          that.handleValidate(ywah_settings_payments_helper.submit_add_onetime_plan);
        });

      that.$chbxIntlPayment
        .off(click)
        .on(click, function () {
          var
            isChecked = $(this).is(':checked'),
            $zipCodeEl = that.element.find('.cc-form-inputs [name="zip"]'),
            $zipCodeWrapper = $zipCodeEl.closest('.form-group').parent();

          $zipCodeWrapper
            .toggle(!isChecked)
            .find('label.error').remove();
          $zipCodeEl.removeClass(classError).val('');
        });

      that.$btnTfaConfirm.off(click).on(click, function () {
        that.handleValidate(ywah_onboarding_helper.submit_onboarding_tfa_verify);
      });

      that.$btnTfaRequestCode.off(click).on(click, function (e) {
        e.preventDefault();
        ywah_onboarding_helper.submit_onboarding_tfa_request(that.$form);
      });

      that.$btnTfaSettingsConfirm.off(click).on(click, function () {
        that.handleValidate(ywah_settings_payments_helper.submit_setting_tfa_verify);
      });

      that.$btnTfaSettingsRequestCode.off(click).on(click, function (e) {
        e.preventDefault();
        ywah_settings_payments_helper.submit_setting_tfa_request_again(that.$form);
      });

      that.$btnTfaUpdatePhone.off(click).on(click, function () {
        that.handleValidate(ywah_settings_payments_helper.submit_setting_tfa_request);
      });

      // Reset form when the modal was hidden
      this.element.closest('.modal').on('hidden.bs.modal', function () {
        var $that = $(this),
          $form = $that.find('form');

        $form[0].reset();
        $form.find('label.error').html('');
        $form.find('.general-error').remove();
      });
    },

    handleValidate: function (cb) {
      var that = this;

      if (typeof cb === 'function') {
        _cb = cb;
      }

      this.$form.validate({
        rules: {
          name: {
            required: true,
            regexName: true,
            maxlength: 100,
          },
          first_name: {
            required: true,
            regexName: true,
            maxlength: 50,
          },
          last_name: {
            required: true,
            regexName: true,
            maxlength: 50,
          },
          email: {
            required: true,
            regexEmail: true,
            maxlength: 100,
          },
          password: {
            required: true,
            regexPwd: true
          },
          newPassword: {
            required: true,
            regexPwd: true
          },
          confirmPassword: {
            required: true,
            regexPwd: true,
            equalTo: "#newPassword"
          },
          agree: {
            required: true
          },
          card_number: {
            required: true,
            creditcard: true
          },
          card_exp: {
            required: true,
            expiration: true
          },
          card_cvv: {
            required: true,
            minlength: 3,
            number: true
          },
          gift_code: {
            required: true
          },
          zip: {
            regexZip: true,
            required: true,
            minlength: 5,
            maxlength: 5,
          },
          phone_number: {
            required: true,
            minlength: 4,
            number: true
          },
          otp_code: {
            required: true,
            number: true,
            minlength: 6,
            maxlength: 6,
          },
        },
        messages: {
          name: 'Please enter a valid name',
          first_name: 'Please enter a valid first name',
          last_name: 'Please enter a valid last name',
          email: 'Please enter a valid email address',
          password: 'Password should be at least 8 characters, with at least 1 upper case, 1 lower case, and 1 number',
          confirmPassword: "Passwords don't match",
          newPassword: {
            required: 'Please enter a valid password',
            regexPwd: 'Password should be at least 8 characters, with at least 1 upper case, 1 lower case, and 1 number'
          },
          agree: 'Please agree to our policy',
          card_number: 'Please enter valid credit card number',
          card_exp: 'Please enter a valid expiration',
          card_cvv: 'Please enter a valid CVV Code',
          gift_code: 'Please enter a valid number',
          zip: 'Please enter a valid Zip Code',
          phone_number: 'Please enter a valid Phone Number',
          otp_code: 'Invalid Code.',
        },
        submitHandler: function (form, e) {
          var useRecaptcha = that.options.useRecaptcha;

          // Support data-use-captcha and has callback function only
          if (useRecaptcha && typeof _cb === 'function') {
            e.preventDefault();

            $.fn.reCaptchaExecute();

            formCallback = _cb;
            formElement = form;
            that.resetCallBackFunc();
          } else {
            if (typeof _cb === 'function') {
              _cb(form);
              that.resetCallBackFunc();
            } else {
              that.callAjax(form);
            }
          }
        },
        errorPlacement: function (error, element) {
          (element.closest(formGroupWrap)).after(error.addClass(classValidError));
        }
      });
    },

    resetCallBackFunc: function() {
      setTimeout(function() {
        _cb = null;
      }, 0);
    },

    onSubmitEvents: function (token) {
      if (typeof formCallback === 'function' && formElement !== null) {
        var
          $form = $(formElement),
          $tokenEl = $form.find('input[name="recaptcha_token"]')
        ;

        if ($tokenEl.length) {
          $tokenEl.val(token);
        } else {
          $form.append('<input type="hidden" name="recaptcha_token" value="' + token + '">');
        }

        formCallback($form.get(0));

        // Reset values
        formCallback = formElement = null;
        $.fn.reCaptchaReset();
      }
    },

    redeemCouponCode: function (form, data) {
      var
        $that = this,
        $checkoutForm = $('.ywah-onboarding-checkout'),
        $checkoutFormInputs = $checkoutForm.find('.cc-form-inputs'),
        $planCode = $checkoutForm.find('input[name="plan_code"]'),
        $promoDescBlk = $checkoutForm.find('[data-subscribe-promo]'),
        $chbxIntlPayment = $checkoutForm.find('.intl-payment-wrap'),
        $intlPayment = $chbxIntlPayment.find('[data-intl-payment]'),
        $newPromoCodeInput = $('.ywah-onboarding-checkout-new').find('input[name="gift_code"]'),
        $el = $(form),
        $inputForm = $el.find('input[name="gift_code"]'),
        promoCode = $inputForm.val(),
        $onePlanCheckout = $el.find('input[name="one_checkout"]'),
        onePlanCheckout = $onePlanCheckout ? parseInt($onePlanCheckout.val()) : 0,
        $modal = $el.closest('.modal-gift-code'),
        idModal = $modal.attr('id'),
        $btnCallModal = $('[data-target="#' + idModal + '"]'),
        $codeBlock = $('<div class="apply-code"></div>'),
        isNewSubscribeFlow = typeof helper_gateway.isNewSubscribeFlow !== 'undefined' && helper_gateway.isNewSubscribeFlow,
        applyText = isNewSubscribeFlow ? 'PROMO CODE: ' : 'APPLIED: ',
        tag = isNewSubscribeFlow ? 'div' : 'p',
        temp = [
          '<' + tag + '>' + applyText + '<span>' + promoCode + '</span>' + '</' + tag + '>',
          (isNewSubscribeFlow ? '<span class="cancel-icon">&times;</span>' : '<span class="cancel-text">CANCEL</span>'),
          '<input type="hidden" name="gift_code" value="' + promoCode + '">',
        ].join('');

      $inputForm.val('');
      $modal.modal('hide');
      $codeBlock.append(temp);
      $btnCallModal.hide().after($codeBlock);
      $newPromoCodeInput.val(promoCode);

      if (isNewSubscribeFlow) {
        $('[data-gift-card-open-popup-btn]').parent().addClass(classHidden);
        $('[data-promo-code-open-popup-btn]').parent().addClass('col-12').removeClass('col-6');
      } else {
        $('[data-gift-card-open-popup-btn]').addClass(classHidden).removeClass('d-inline-block');
      }

      if ($btnCallModal.hasClass('d-inline-block')) {
        $btnCallModal.removeClass('d-inline-block').addClass(classHidden);
      }

      if (typeof data.planPriceP !== 'undefined' && data.planPriceP) {
        $('[data-ns-plan-item]:checked').parent('label').find('[data-ns-plan-item-price]').html('$' + data.planPriceP);
      }

      if (onePlanCheckout) {
        if (typeof data.noccrequired !== 'undefined' && data.noccrequired) {
          $that.noCcRequired = true;
          $planCode.val(data.plan_code);
          $promoDescBlk.html(data.plan_data.name ?
            '<h5>' +
            '  <span>SUBSCRIPTION: </span>' +
            '  ' + data.plan_data.name +
            '</h5>' : ''
          ).removeClass(classHidden).css('display', 'block');

          $checkoutFormInputs.addClass(classHidden);
          $checkoutForm.find('.validate-error').hide();
          $chbxIntlPayment.addClass(classHidden);
        }
      } else {
        $('[data-onboarding-subscribe]').trigger('applied-code', data);
      }

      $codeBlock.find('span').bind('click', function () {
        $codeBlock.remove();
        $btnCallModal.show();
        $newPromoCodeInput.val('');
        if (typeof helper_gateway.isNewSubscribeFlow !== 'undefined' && helper_gateway.isNewSubscribeFlow) {
          $btnCallModal.removeClass(classHidden).addClass('d-inline-block');
          $('[data-gift-card-open-popup-btn]').parent().removeClass(classHidden);
          $('[data-promo-code-open-popup-btn]').parent().addClass('col-6').removeClass('col-12');
        } else {
          $('[data-promo-code-open-popup-btn]').removeClass(classHidden).addClass('d-inline-block');
          $('[data-gift-card-open-popup-btn]').removeClass(classHidden).addClass('d-inline-block');
        }

        if (onePlanCheckout) {
          if ($that.noCcRequired) {
            $that.noCcRequired = false;
            $planCode.val($checkoutForm.find('input[name="original_plan_code"]').val());
            $promoDescBlk.addClass(classHidden).css('display', 'none');

            $checkoutForm.find('.validate-error').hide();
            $checkoutFormInputs.removeClass(classHidden);
            $checkoutFormInputs.find('label.error').hide();
            $checkoutFormInputs.find('input[type=text]').val('').removeClass(classError);
            $checkoutFormInputs.find('.ico.ico-cards').removeClass().addClass('ico ico-cards');
            $chbxIntlPayment.removeClass(classHidden);
            $intlPayment.prop('checked', false).triggerHandler('click');
          }
        } else {
          $('[data-onboarding-subscribe]').trigger('cancel-code');
        }
      });
    },

    callAjax: function (element) {
      var that = this,
        $form = $(element),
        formData = $form.serializeArray(),
        $submitBtn = $form.find('button[type="submit"]'),
        $errMsg = $form.find('.general-error');

      $submitBtn.prop('disabled', true);

      $.fn.callAjaxGlobal({
        url: yahAjax.ajaxurl,
        method: that.options.method,
        data: formData,
        cache: false,
        loader: true
      }).done(function (res) {
        // Hide modal window
        if (res.success) {
          $('.modal').modal('hide');
        } else {
          $errMsg.remove();
          $form.append('<label class="general-error text-danger">' + res.data.message + '</label>');
        }

        $submitBtn.prop('disabled', false);
      }).fail(function (request, status, error) {
        $submitBtn.prop('disabled', false);
      });
    },

    handleCouponCode: function (element) {
      var that = this,
        formData = $(element).serializeArray();

      $(element).find('.text-danger').remove();

      $.ajax({
        type: that.options.method,
        url: that.options.url,
        data: formData,
        cache: false,
        success: function (res) {
          if (res.success) {
            $('#couponCode').modal('hide');
            $('#couponCode').trigger('coupon-code-apply', res);
          } else {
            // console.log("Fail scenario here...");
          }
        },
        error: function (err) {
          console.log(err);

          var errMess = '<label class="text-danger">' + err.statusText + '</label>';
          $(element).append(errMess);
        }
      });
    },

    handleUpdatePassword: function (element) {
      var that = this,
        $form = $(element),
        formData = $form.serializeArray(),
        $submitBtn = $form.find('button[type="submit"]'),
        $errMsg = $form.find('.general-error');

      $errMsg.html('');
      $submitBtn.prop('disabled', true);

      $.fn.callAjaxGlobal({
        url: yahAjax.ajaxurl,
        method: that.options.method,
        data: formData,
        cache: false,
        loader: true
      }).done(function (res) {
        if (res.success) {
          $('.modal').modal('hide');
          window.location.reload();
        } else {
          // $form.find('#password, #newPassword, #confirmPassword').val('');
          $errMsg.html(res.data.message).show();
        }

        $submitBtn.prop('disabled', false);
      }).fail(function (request, status, error) {
        $submitBtn.prop('disabled', false);
      });
    },

    destroy: function () {
      $.removeData(this.element[0], pluginName);
    }
  };

  $.fn[pluginName] = function (options, params) {
    return this.each(function () {
      var instance = $.data(this, pluginName);
      if (!instance) {
        $.data(this, pluginName, new Plugin(this, options));
      } else if (instance[options]) {
        instance[options](params);
      }
    });
  };

  $.fn[pluginName].defaults = {
    method: 'POST',
    url: yahAjax.ajaxurl
  };

  $(function () {
    $('[data-' + pluginName + ']')[pluginName]();
  });

}(jQuery, window));
