var Myw = Myw || {};

Myw.Member = {
    updateLike: function ( wpId, liked, callback) {
        jQuery.ajax({
            url: mywajax.ajaxurl,
            dataType: 'json',
            method: 'POST',
            data: {
                'action' : 'like_video',
                'wpId'   : wpId,
                'liked'  : liked
            },
            success: function (data) {
                callback(data);
            }
        });
    },
    updateArticleLike: function ( wpId, liked, callback) {
        jQuery.ajax({
            url: mywajax.ajaxurl,
            dataType: 'json',
            method: 'POST',
            data: {
                'action' : 'like_article',
                'wpId'   : wpId,
                'liked'  : liked
            },
            success: function (data) {
                callback(data);
            }
        });
    },
    postComment: function (asset_id, title, comment, callback) {
        jQuery.ajax({
            url: mywajax.ajaxurl,
            dataType: 'json',
            method: 'POST',
            data: {
                'action': 'post_comment',
                'asset_id': asset_id,
                'title': title,
                'comment': comment
            },
            success: function (data) {
                callback(data);
            }
        });
    }
};

Myw.Common = {
    post: function (data, callback) {
        jQuery.ajax({
            url: mywajax.ajaxurl,
            dataType: 'json',
            method: 'POST',
            data: data,
            success: function (data) {
                callback(data);
            }
        });
    },
    postConfirmPage: function (title, tagline, redirect) {
        $title = jQuery('<input>')
            .attr('name', 'title')
            .attr('type', 'text')
            .val(title);
        $tagline = jQuery('<input>')
            .attr('name', 'tagline')
            .attr('type', 'text')
            .val(tagline);
        $form = jQuery('<form>')
            .attr('method', 'post')
            .attr('action', redirect)
            .attr('style', 'display:none;')
            .append($title)
            .append($tagline)
            .appendTo("body")
            .submit();
    },
    postReactivatePage: function (plan, promoCode, redirect) {
        $plan = jQuery('<input>')
            .attr('name', 'plan')
            .attr('type', 'text')
            .val(plan);
        $promo = jQuery('<input>')
            .attr('name', 'promoCode')
            .attr('type', 'text')
            .val(promoCode);
        $form = jQuery('<form>')
            .attr('method', 'post')
            .attr('action', redirect)
            .attr('style', 'display:none;')
            .append($plan)
            .append($promo)
            .appendTo("body")
            .submit();
    },
    formatAuthorName: function (author, append_dot) {
        if (typeof author === 'undefined' || author.length < 1) {
            return '';
        }

        var author_names = author.split(" ");
        if (author_names.length === 1) {
            return author;
        }

        return author_names[0] + ' ' + author_names[1].substring(0, 1) + (append_dot ? '.' : '');
    },
};

