;(function ($, window, undefined) {
  'use strict';

  var pluginName = 'playlist-framework-modal',
    click = 'click.' + pluginName,
    input = 'input.' + pluginName,
    hideClass = 'd-none',
    inProgress = false,
    currentVideoId = null;

  function Plugin(element, options) {
    this.element = $(element);
    this.options = $.extend({}, $.fn[pluginName].defaults, this.element.data(), options);
    this.init();
  }

  Plugin.prototype = {
    init: function () {
      var $that = this;

      $that.$form = $that.element.find('form.form');
      $that.$loading = $that.element.find('.img-loading');
      $that.$modalErrorMessage = $that.element.find('.general-error');

      $that.$newPlaylistNameInput = $that.element.find('[data-input-new-playlist]');
      $that.$btnAddPlaylist = $that.element.find('[data-btn-add-playlist]');
      $that.$btnUpdatePlaylist = $that.element.find('[data-btn-update-playlist]');
      $that.selectedPlaylists = '[data-radiobox]';
      $that.currentModalId = null;

      $that.$modalDeletePlaylist = $('[ data-playlist-delete-modal]');
      $that.modalDeletePlaylistId = $that.$modalDeletePlaylist.attr('id');
      $that.$modalBtnDelete = $that.$modalDeletePlaylist.find('[data-btn-accept]');
      $that.$modalDeleteMessage = $that.$modalDeletePlaylist.find('.general-error');
      $that.$modalBtnDeleteCached = null;

      $that.handleEvents();
    },
    handleEvents: function () {
      var $that = this;

      $that.element.on('show.bs.modal', function () {
        currentVideoId = $that.$btnUpdatePlaylist.data('video-id');

        $that.$modalErrorMessage.html('');

        $that.getAllPlaylistsEvent(function (res) {
          $that.renderUserPlaylist(res, true);
        });
      });

      $that.$modalDeletePlaylist.on('hidden.bs.modal', function () {
        $('body').addClass('modal-open');
      });

      $that.$newPlaylistNameInput.off(input).on(input, function () {
        $that.$modalErrorMessage.html('');
        $that.$btnAddPlaylist.toggleClass('d-none', !$(this).val().length);
      });

      $that.$newPlaylistNameInput.on('keypress', function (e) {
        if (e.which === 13) { // enter key
          e.preventDefault();
          $that.$btnAddPlaylist.trigger(click);
        }
      });

      $that.$btnAddPlaylist.off(click).on(click, function () {
        $that.$modalErrorMessage.html('');

        $that.addPlaylistEvent(function (res) {
          $that.renderUserPlaylist(res, false);
        });
      });

      $that.$btnUpdatePlaylist.off(click).on(click, function (e) {
        e.preventDefault();

        $that.$modalErrorMessage.html('');
        $that.updatePlaylistEvent($(this));
      });

      $that.element.on(click, '[data-btn-delete-playlist]', function (e) {
        e.preventDefault();

        $that.$modalBtnDeleteCached = $(this);
        $that.$modalErrorMessage.html('');
        $that.$modalDeleteMessage.html('').removeClass('mb-3');
      });

      $that.$modalBtnDelete.off(click).on(click, function (e) {
        e.preventDefault();

        $that.$modalDeleteMessage.html('').removeClass('mb-3');
        $that.deletePlaylistEvent();
      });
    },
    getAllPlaylistsEvent: function (callback) {
      var $that = this;

      $that.showLoadingImg();
      $.fn.callAjaxGlobal({
        url: yahAjax.ajaxurl,
        method: 'GET',
        data: {
          action: 'get_playlists',
          videoId: currentVideoId,
          formCheck: this.$btnUpdatePlaylist.data('form-check'),
        },
        loader: true
      }).done(function (res) {
        if (res && res.success) {
          if (typeof callback === 'function') {
            callback(res.data);
          }
        }

        $that.hideLoadingImg();
      }).fail(function (request, status, error) {
      });
    },
    getSelectedPlaylists: function () {
      var
        selectedPlaylistIds = [],
        $playlistItems = $(this.selectedPlaylists).filter(':checked');

      $.each($playlistItems, function (idx, el) {
        var playlistId = $(el).data('playlist-id');
        if (playlistId) {
          selectedPlaylistIds.push(playlistId);
        }
      });

      return selectedPlaylistIds;
    },
    updateVideoMenuContext: function (inPlaylist) {
      $('#playlist-action-menu-' + currentVideoId).text((inPlaylist ? 'Remove from' : 'Add to') + ' Playlist');
    },
    addPlaylistEvent: function (callback) {
      var
        $that = this,
        $nameInput = $that.$newPlaylistNameInput;

      if (!inProgress) {
        inProgress = true;
        $that.showLoadingImg();

        $.fn.callAjaxGlobal({
          url: yahAjax.ajaxurl,
          method: 'POST',
          data: {
            action: $nameInput.data('action'),
            playlistName: $nameInput.val(),
            formCheck: $nameInput.data('form-check'),
          },
          loader: true
        }).done(function (res) {
          if (res && res.success) {
            if (typeof callback === 'function') {
              callback(res.data);
            }
          }
          else {
            $that.$modalErrorMessage.html(res.data.message);
          }
          inProgress = false;
          $that.hideLoadingImg();
        }).fail(function (request, status, error) {
          inProgress = false;
          $that.hideLoadingImg();
          $that.$modalErrorMessage.html('Invalid request.');
        });
      }
    },
    updatePlaylistEvent: function ($updateBtn) {
      var
        $that = this,
        selectedPlaylistIds = $that.getSelectedPlaylists();

      $that.$btnUpdatePlaylist.prop('disabled', true);
      if (!inProgress) {
        inProgress = true;
        $that.showLoadingImg();

        $.fn.callAjaxGlobal({
          url: yahAjax.ajaxurl,
          method: 'POST',
          data: {
            action: $updateBtn.data('action'),
            videoId: currentVideoId,
            playlistIds: selectedPlaylistIds ? selectedPlaylistIds.join() : null,
            formCheck: $updateBtn.data('form-check'),
          },
          loader: true
        }).done(function (res) {
          if (res && res.success) {
            $that.hideModal();
            $that.updateVideoMenuContext(res.data.inPlaylist);
          }
          else {
            $that.$modalErrorMessage.html(res.data.message);
          }
          inProgress = false;
          $that.hideLoadingImg();
          $that.$btnUpdatePlaylist.prop('disabled', false);
        }).fail(function (request, status, error) {
          inProgress = false;
          $that.hideLoadingImg();
          $that.$btnUpdatePlaylist.prop('disabled', false);
          $that.$modalErrorMessage.html('Invalid request.');
        });
      }
    },
    deletePlaylistEvent: function () {
      var
        $that = this,
        $formDelete = $that.$modalDeletePlaylist.find('form.form'),
        $formLoading = $that.$modalDeletePlaylist.find('.img-loading'),
        $btnDelete = $that.$modalBtnDeleteCached,
        formCheck = $btnDelete.parents('form').find('[name="delete-nonce"]').val();

      $that.$modalBtnDelete.prop('disabled', true);
      if (!inProgress) {
        inProgress = true;
        $formDelete.addClass(hideClass);
        $formLoading.removeClass(hideClass);

        $.fn.callAjaxGlobal({
          url: yahAjax.ajaxurl,
          method: 'POST',
          data: {
            action: $btnDelete.data('action'),
            playlistId: $btnDelete.data('playlist-id'),
            videoId: currentVideoId,
            formCheck: formCheck
          },
          loader: true
        }).done(function (res) {
          if (res && res.success) {
            $that.$modalDeletePlaylist.modal('hide');
            $btnDelete.closest('.form-checkbox').remove();
            $that.updateVideoMenuContext(res.data.inPlaylist);
          }
          else {
            $that.$modalDeleteMessage.html(res.data.message).addClass('mb-3');
          }
          $that.$modalBtnDelete.prop('disabled', false);
          inProgress = false;
          $formDelete.removeClass(hideClass);
          $formLoading.addClass(hideClass);
        }).fail(function (request, status, error) {
          $that.$modalDeleteMessage.html('Invalid request.').addClass('mb-3');
          $that.$modalBtnDelete.prop('disabled', false);
          inProgress = false;
          $formDelete.removeClass(hideClass);
          $formLoading.addClass(hideClass);
        });
      }
    },
    renderUserPlaylist: function (data, reset) {
      var
        $that = this,
        $playlistWrapper = $that.element.find('.playlist-wrapper'),
        playlistData = data.playlistData,
        playlistHtml = '',
        hasInPlaylistItem = false;

      if (reset) {
        $playlistWrapper.empty();
      }

      for (var i = 0; i < playlistData.length; i++) {
        var
          playlistId = playlistData[i].playlistId,
          playlistName = playlistData[i].playlistName,
          inList = playlistData[i].inList,
          itemHtml =
            '<div class="form-checkbox">' +
            '  <div class="form-group-wrap">' +
            '    <div class="title-wrap">' +
            '      <label for="playlist_name_' + playlistId + '">' +
            '        <input type="checkbox" name="playlist" id="playlist-item-' + playlistId + '"' + (inList ? ' checked ' : '') +
            '               data-radiobox data-playlist-id="' + playlistId + '">' +
            '        <span class="radiomark"></span>' +
            '        <span>' + playlistName + '</span>' +
            '      </label>' +
            '      <div class="radio-detail">' +
            '        <p data-btn-delete-playlist data-action="delete_playlist" data-playlist-id="' + playlistId + '" data-toggle="modal" data-target="#' + this.modalDeletePlaylistId + '">Delete</p>' +
            '      </div>' +
            '    </div>' +
            '  </div>' +
            '</div>';

        playlistHtml += itemHtml;
        hasInPlaylistItem = !!inList;
      }

      $playlistWrapper.append(playlistHtml);

      if (!hasInPlaylistItem) {
        $that.updateVideoMenuContext(false);
      }

      $that.resetForm();
    },
    showModal: function (data) {
      var $that = this;

      $that.currentModalId = data.modalId;
      $($that.currentModalId).modal('show');

      $($that.currentModalId).on('hide.bs.modal', function () {
        $that.resetForm();
      });
    },
    hideModal: function () {
      $(this.currentModalId).modal('hide');
    },
    showLoadingImg: function () {
      this.$loading.removeClass(hideClass);
      this.$form.addClass(hideClass);
    },
    hideLoadingImg: function () {
      this.$loading.addClass(hideClass);
      this.$form.removeClass(hideClass);
    },
    resetForm: function () {
      this.$newPlaylistNameInput.val('');
      this.$btnAddPlaylist.addClass('d-none');
    },
    destroy: function () {
      $.removeData(this.element[0], pluginName);
    }
  };

  $.fn[pluginName] = function (options, params) {
    return this.each(function () {
      var instance = $.data(this, pluginName);
      if (!instance) {
        $.data(this, pluginName, new Plugin(this, options));
      }
      else if (instance[options]) {
        instance[options](params);
      }
    });
  };

  $.fn[pluginName].defaults = {
    method: 'POST',
    url: yahAjax.ajaxurl
  };

  $(function () {
    $('[data-' + pluginName + ']')[pluginName]();
  });

}(jQuery, window));
