;(function($, window, undefined) {
  'use strict';
  var pluginName = 'imagecycle',
    body = $('body'),
    win = $(window);

  function Plugin(element, options) {
    this.element = $(element);
    this.options = $.extend({}, $.fn[pluginName].defaults, this.element.data(), options);
    this.init();
  }

  Plugin.prototype = {
    init: function() {
      var changeInterval,
        danceInterval,
        that = this,
        len = this.element.find('li').length,
        indexRandomize = function(min, max) {
          return Math.floor(Math.random() * (max - min + 1)) + min;
        },
        changeIMG = function() {
          var hideEl = that.element.find('li:nth-child('+ indexRandomize(1, 16) +')'),
            showEl = that.element.find('li:nth-child('+ indexRandomize(17, len) +')'),
            run = function(hideEl, showEl){
            var danceIMG,
              nyNew = 180,
              nyOld = 0;

            hideEl
              .find('img')
              .addClass('old');

            showEl
              .find('img')
              .addClass('new')
              .appendTo(hideEl);

            danceIMG = function(){
              nyOld = nyOld - 1;
              nyNew = nyNew - 1;
              hideEl
                .find('.old')
                .css('transform', 'rotateY(' + nyOld + 'deg)');
              hideEl
                  .find('img:not(.old)')
                  .css('transform', 'rotateY(' + nyNew + 'deg)');

              if(nyOld === -90) {
                hideEl
                  .find('.old')
                  .removeClass('old')
                  .appendTo(showEl);
                hideEl
                  .find('.new')
                  .removeClass('new');
              }

              if (nyNew === 0) {
                nyNew = 180;
                nyOld = 0;
                hideEl = that.element.find('li:nth-child('+ indexRandomize(1, 16) +')');
                showEl = that.element.find('li:nth-child('+ indexRandomize(17, len) +')');
                run(hideEl, showEl);
              }
              else{
                setTimeout(danceIMG, 10);
              }
            };
            setTimeout(danceIMG, 2000);
          };

          run(hideEl, showEl);
        };

      changeIMG();
    },
    destroy: function() {
      this.element
        .find('img')
        .removeClass('new old')
        .css('transform', '');
      $.removeData(this.element[0], pluginName);
    }
  };

  $.fn[pluginName] = function(options, params) {
    return this.each(function() {
      var instance = $.data(this, pluginName);
      if (!instance) {
        $.data(this, pluginName, new Plugin(this, options));
      } else if (instance[options]) {
        instance[options](params);
      }
    });
  };

  $.fn[pluginName].defaults = {};

  $(function() {
    $('[data-' + pluginName + ']')[pluginName]();
  });

}(jQuery, window));
