;(function ($, window, undefined) {
  'use strict';

  var
    pluginName = 'global-search',
    hiddenClass = 'd-none',
    liveClassCount = 0,
    ondemandVideoCount = 0,
    ondemandVideosPerPage = 12,
    isOndemandLoaded = false,
    isLiveLoaded = false,
    waypoints
  ;

  function Plugin(element, options) {
    this.element = $(element);
    this.options = $.extend({}, $.fn[pluginName].defaults, this.element.data(), options);
    this.searchKey = typeof this.options.searchKey !== 'undefined' ? this.options.searchKey : null;

    if (this.options.allowedSearch) {
      this.init();
    }
  }

  Plugin.prototype = {
    init: function () {
      var $that = this;

      $that.$searchNotFound = $that.element.find('.search-not-found');

      $that.$ondemandSearchResultWrapper = $that.element.find('[data-ondemand-global-search]');
      $that.$ondemandSearchResultCount = $that.$ondemandSearchResultWrapper.find('[data-ondemand-search-count]');
      $that.$ondemandSearchResults = $that.$ondemandSearchResultWrapper.find('[data-ondemand-search-results]');

      $that.$liveSearchResultWrapper = $that.element.find('[data-live-global-search]');
      $that.$liveSearchResultCount = $that.$liveSearchResultWrapper.find('[data-live-search-count]');
      $that.$liveSearchResults = $that.$liveSearchResultWrapper.find('[data-live-search-results]');

      if ($that.searchKey !== null) {
        $that.getOnDemandVideos(1, function (page, htmlVideos) {
          $that.renderOnDemandVideos(page, htmlVideos);

          $(window).on('scrolled', function () {
            $that.initWaypoints();
          });
        }, false);

        $that.getLivestreamClasses(function (liveClasses) {
          $that.renderLiveClasses(liveClasses);
        });
      } else {
        $that.$searchNotFound.removeClass(hiddenClass);
      }
    },
    getOnDemandVideos: function (page, callback, showLoading) {
      this.destroyWaypoints();

      $.fn.callAjaxGlobal({
        url: yahAjax.ajaxurl,
        method: 'GET',
        data: {
          action: this.options.videoAction,
          keyword: this.searchKey,
          page: page,
          videosPerPage: ondemandVideosPerPage,
          formCheck: this.options.videoNonce,
        },
        loader: showLoading
      }).done(function (res) {
        isOndemandLoaded = true;

        if (res && res.success) {
          ondemandVideoCount = res.data.total;

          if (typeof callback === 'function') {
            callback(page, res.data.ondemandVideos);
          }
        }
      }).fail(function (request, status, error) {
      });
    },
    getLivestreamClasses: function (callback) {
      $.fn.callAjaxGlobal({
        url: yahAjax.ajaxurl,
        method: 'GET',
        data: {
          action: this.options.liveAction,
          keyword: this.searchKey,
          formCheck: this.options.liveNonce,
        },
        loader: true
      }).done(function (res) {
        isLiveLoaded = true;

        if (res && res.success) {
          liveClassCount = res.data.total;

          if (typeof callback === 'function') {
            callback(res.data.liveClasses);
          }
        }
      }).fail(function (request, status, error) {
      });
    },
    renderOnDemandVideos: function (page, htmlVideos) {
      var $that = this,
        start = page * ondemandVideosPerPage;

      if (isOndemandLoaded && isLiveLoaded && ondemandVideoCount < 1 && liveClassCount < 1) {
        $that.$searchNotFound.removeClass(hiddenClass);
      } else if (ondemandVideoCount > 0) {
        $that.$ondemandSearchResultWrapper.removeClass(hiddenClass);
        $that.$ondemandSearchResultCount.html(ondemandVideoCount + ' results');

        if (page > 1) {
          $that.$ondemandSearchResults.append(htmlVideos);
        } else {
          $that.$ondemandSearchResults.html(htmlVideos);
        }

        if (ondemandVideoCount > start) {
          waypoints = new Waypoint({
            element: $that.$ondemandSearchResults,
            handler: function (direction) {
              if (direction === 'down') {
                $that.getOnDemandVideos(page + 1, function (nextPage, htmlVideos) {
                  $that.renderOnDemandVideos(nextPage, htmlVideos);
                }, true);
              }
            },
            enabled: false,
            offset: 'bottom-in-view'
          });
        } else {
          $that.destroyWaypoints();
        }
      }
    },
    renderLiveClasses: function (liveClasses) {
      var $that = this;

      if (isOndemandLoaded && isLiveLoaded && ondemandVideoCount < 1 && liveClassCount < 1) {
        $that.$searchNotFound.removeClass(hiddenClass);
      } else if (liveClassCount > 0) {
        $that.prepareLiveClassData(liveClasses);
      }
    },
    prepareLiveClassData: function (liveClasses) {
      var
        $that = this,
        liveClassData = typeof liveClasses !== 'undefined' && liveClasses ? liveClasses : [],
        totalItems = liveClassData.length,
        zoneName = moment.tz.guess(),
        today = moment(),
        tomorrow = moment().add(1, 'day'),
        todayTimestamp = today.unix(),
        listClasses = []
      ;

      for (var i = 0; i < totalItems; i++) {
        var
          eventData = liveClassData[i],
          classTimeStamp = eventData.endTimeZonedStamp,
          isAvailable = !eventData.isCanceled && classTimeStamp > todayTimestamp,
          diffInSeconds = eventData.startTimeZonedStamp - todayTimestamp,
          isToday = today.isSame(moment(eventData.startTimeZonedStamp, 'X'), 'day'),
          isTomorrow = tomorrow.isSame(moment(eventData.startTimeZonedStamp, 'X'), 'day');

        if (isAvailable) {
          if (isAvailable) {
            eventData.diffInSeconds = diffInSeconds;
            eventData.enableCtaBtn = false;
            eventData.showComingSoon = false;

            if (typeof eventData.startDateTimeZoned !== 'undefined') {
              var zoned = moment(eventData.startDateTimeZoned);

              eventData.dayOfWeek = zoned.tz(zoneName).format('dddd');
              eventData.startTime = zoned.tz(zoneName).format('h:mm').replace(':00', '');
              eventData.startMeridien = zoned.tz(zoneName).format('a');
              eventData.locationTimeZone = zoned.tz(zoneName).format('z');

              if (isToday) {
                eventData.dayOfWeek = 'Today';
              } else if (isTomorrow) {
                eventData.dayOfWeek = 'Tomorrow';
              }

              // Override some values
              if (diffInSeconds <= $that.options.timeAccess) {
                eventData.startMeridien = 'min';

                var diffMin = Math.floor(diffInSeconds / 60);

                if (diffMin > 0) {
                  eventData.dayOfWeek = 'Starts In';
                  eventData.startTime = diffMin;
                } else {
                  eventData.dayOfWeek = 'Ends In';
                  eventData.startTime = eventData.duration + diffMin;
                }

                eventData.enableCtaBtn = true;
              } else if (diffInSeconds <= $that.options.timeComingSoon) {
                eventData.showComingSoon = true;
              }
            }

            listClasses.push(eventData);
          }
        }
      }

      if (listClasses.length > 0) {
        $that.$liveSearchResultWrapper.removeClass(hiddenClass);
        $that.$liveSearchResultCount.html(listClasses.length + ' results');
        $that.$liveSearchResults.html($that.renderSlickItems(listClasses));
        setTimeout(function () {
          $that.initSlick();
        }, 500);
      } else {
        if (isOndemandLoaded && ondemandVideoCount < 1) {
          $that.$searchNotFound.removeClass(hiddenClass);
        }
      }
    },
    renderSlickItems: function (data) {
      var
        $that = this,
        htmlLiveClasses = ''
      ;

      data.forEach(function (event) {
        var
          eventId = 'item-' + event.siteId + '_' + event.classId,
          level = event.level ? ('<span>' + event.level + '</span>') : '',
          teacherName = event.isSub ? ('<span class="substitute">' + event.subStaff + '</span>' + '<br>' + event.teacherName) : event.teacherName,
          eventClass = ' avoid-click',
          joinBtnAttrs = $that.prepareCtaBtnAttrs(event),
          joinBtn = '<a href="#"' + joinBtnAttrs + '>' + $that.options.ctaLiveText + '</a>',
          upgradeBtn = '<a href="' + $that.options.ctaUpgradeUrl + '">' + $that.options.ctaUpgradeText + '</a>',
          eventCtaBtn = '',
          ctaClass = ' no-cta'
        ;

        if (event.enableCtaBtn) {
          eventClass = ' clickable';
          eventCtaBtn = $that.options.allowedAccess ? joinBtn : upgradeBtn;
          ctaClass = '';
        }

        if (event.showComingSoon) {
          eventCtaBtn = '<span>' + $that.options.comingSoonText + '</span>';
          ctaClass = ' coming-soon';
        }

        htmlLiveClasses +=
          '<div class="col-md-4">' +
          '  <div class="live-content-item' + eventClass + '" data-target="' + eventId + '">' +
          '    <div class="live-content-item__info">' +
          '      <div class="live-content__avatar"><img src="' + event.teacherImg + '" alt=""></div>' +
          '      <div class="live-content-item__description">' +
          '        <h5>' + event.title + '</h5>' +
          '        <p class="context-1">' + teacherName + '</p>' +
          '        <p class="context-1">' + level + '<span>' + event.duration + ' min</span></p>' +
          '        <p class="live-content-item__description-watch' + ctaClass + '">' + eventCtaBtn + '</p>' +
          '      </div>' +
          '    </div>' +
          '    <div class="live-content-item__schedule">' +
          '      <p class="context-3">' + event.dayOfWeek + '</p>' +
          '      <p class="live-content-item__schedule-time">' + event.startTime + '<span>' + event.startMeridien + '</span></p>' +
          '      <p class="context-3 live-content-item__schedule-zone">' + event.locationTimeZone + '</p>' +
          '    </div>' +
          '  </div>' +
          '  <div class="search-cta-result d-none"></div>' +
          '</div>';
      });

      return htmlLiveClasses;
    },
    prepareCtaBtnAttrs: function (event) {
      var
        durationMin = parseInt(event.duration), //in minutes
        duration = isNaN(durationMin) ? (-30 * 60) : -60 * durationMin, //in seconds
        endStamp = parseInt(event.startTimeZonedStamp) - duration
      ;

      return ' data-btn-livestream-join ' +
        'data-site-id="' + event.siteId + '" data-class-id="' + event.classId + '"' +
        'data-class-schedule-id="' + event.classSchedId + '" data-room-name="' + event.resourceName + '"' +
        'data-loc-id="' + event.locId + '" data-loc-name="' + event.location + '"' +
        'data-title="' + event.title + '" data-teacher="' + event.teacherName + '"' +
        'data-is-free="0" data-level="' + event.level + '" data-yoga-type="' + event.yogaType + '"' +
        'data-start-stamp="' + event.startTimeZonedStamp + '" data-duration="' + event.duration + '"' +
        'data-end-stamp="' + endStamp + '" data-plugin-name="' + pluginName + '"';
    },
    initSlick: function () {
      var $that = this,
        $slickContainer = $that.$liveSearchResults,
        $slickControls = $that.$liveSearchResultWrapper.find('.slider-control .slider-nav');

      if (liveClassCount <= 3) {
        return;
      }

      $slickContainer.slick({
        slidesToShow: 3,
        slidesToScroll: 1,
        dots: true,
        infinite: false,
        arrows: true,
        appendArrows: $slickControls,
        appendDots: $slickControls,
        prevArrow: '<span class="slick-prev" role="button"></span>',
        nextArrow: '<span class="slick-next" role="button"></span>',
        responsive: [
          {
            breakpoint: 1024,
            settings: {
              slidesToShow: 2,
            }
          },
          {
            breakpoint: 768,
            settings: {
              dots: false,
              arrows: false,
              slidesToShow: 1,
              centerMode: true,
              centerPadding: '20%'
            }
          }
        ]
      });
    },
    initWaypoints: function () {
      if (waypoints) {
        waypoints.enable();
      }
    },
    destroyWaypoints: function () {
      if (waypoints) {
        waypoints.destroy();
      }
    },
    renderErrorMessage: function (data) {
      var msg = data.message;

      $('[data-target="item-' + data.itemId + '"]').parent().find('.search-cta-result')
        .html(msg)
        .toggleClass(hiddenClass, typeof msg === 'undefined' || msg === '');
    },
    destroy: function () {
      $.removeData(this.element[0], pluginName);
    }
  };

  $.fn[pluginName] = function (options, params) {
    return this.each(function () {
      var instance = $.data(this, pluginName);

      if (!instance) {
        $.data(this, pluginName, new Plugin(this, options));
      } else if (instance[options]) {
        instance[options](params);
      }
    });
  };

  $.fn[pluginName].defaults = {
    ctaLiveText: 'Live Now',
    comingSoonText: 'Coming Soon',
    ctaUpgradeText: 'Upgrade',
    ctaUpgradeUrl: '/account/#payments',
    timeAccess: 5 * 60,
    timeComingSoon: 120 * 60,
    allowedAccess: 0,
    allowedSearch: 1,
  };

  $(function () {
    $('[data-' + pluginName + ']')[pluginName]();
  });

}(jQuery, window));
