;(function ($, window, undefined) {
  'use strict';

  var pluginName = 'vertical-line';

  function Plugin(element, options) {
    this.element = $(element);
    this.options = $.extend({}, $.fn[pluginName].defaults, this.element.data(), options);
    this.init();
  }

  Plugin.prototype = {
    init: function () {
      this.cacheDOM();
      this.bindEvent();
    },

    cacheDOM: function () {
      this.$win = $(window);
      this.controller = null;
    },

    bindEvent: function () {
      var that = this;

      this.$win
        .on('load resized scrollmagic:change', function () {
          that.handleScrollMagic();
        });
    },

    initScrollMagic: function () {
      var $el = this.element,
        $triggerElement = $el.next(),
        $prevSection = $el.closest('section');

      var getDuration = function (tag, section, triggerEl) {
        var tagWidth = tag.outerWidth(true),
          sectionHeight = section.outerHeight(true),
          offsetSection = section.offset().top,
          offsetTriggerEl = triggerEl.offset().top,
          spaceBetweenOffset = offsetTriggerEl - offsetSection;

        return sectionHeight - tagWidth - spaceBetweenOffset;
      };

      this.controller = new ScrollMagic.Controller({
        globalSceneOptions: {
          triggerHook: 'onLeave'
        }
      });

      if ($triggerElement.length) {
        var scene = new ScrollMagic.Scene({
          triggerElement: $triggerElement[0],
          offset: "-" + $('[data-menu-header]').outerHeight(),
          duration: getDuration($el, $prevSection, $triggerElement)
        })
          .setPin($el[0], {pushFollowers: false})
          .addTo(this.controller);
      }
    },

    destroyScrollMagic: function () {
      if (this.controller) {
        this.controller.destroy(true);
        this.controller = null;
      }
    },

    handleScrollMagic: function () {
      if ($.fn.isMobile()) {
        this.destroyScrollMagic();
        return;
      }

      this.destroyScrollMagic();
      this.initScrollMagic();
    },

    destroy: function () {
      $.removeData(this.element[0], pluginName);
    }
  };

  $.fn[pluginName] = function (options, params) {
    return this.each(function () {
      var instance = $.data(this, pluginName);
      if (!instance) {
        $.data(this, pluginName, new Plugin(this, options));
      } else if (instance[options]) {
        instance[options](params);
      }
    });
  };

  $.fn[pluginName].defaults = {};

  $(function () {
    $('.wrapper-vertical-line')[pluginName]();
  });

}(jQuery, window));
