;(function ($, window, undefined) {
  'use strict';

  var pluginName = 'account-plan-management',
    hideClass = 'd-none',
    activeClass = 'active',
    invisibleClass = 'invisible',
    planList = {},
    click = 'click.' + pluginName,
    beforeRedeemData = {},
    currentSubscriptionId = null,
    isPromoFlow = false,
    isFreeTrialFlow = false,
    checkoutPlan = null,
    checkoutPlanType = null,
    refreshPage = false,
    formCheck = null,
    isDeactivateFlow = false,
    initialCancellationReason = '',
    currentDeactivateStep = 0,
    cartFootnote = '';

  function Plugin(element, options) {
    this.element = $(element);
    this.options = $.extend({}, $.fn[pluginName].defaults, this.element.data(), options);
    this.init();
  }

  Plugin.prototype = {
    init: function () {
      this.$form = this.element.find('[data-form-validate]');
      this.$loading = this.element.find('.img-loading');
      this.$formErrorMsg = this.element.find('.form-error-msg');

      this.$headerPlan = this.element.find('[data-header-plan]');
      this.$accountSubscriptionList = this.element.find('[data-account-plan-new]');
      this.$deactivateSubscription = this.element.find('[data-item-deactivate-only]');
      this.$cardWrapper = this.element.find('.account-plan-card-wrapper');
      this.$planListItem = this.element.find('[data-plan-list-item]');
      this.$promoGroup = this.element.find('[data-promo-group]');

      this.$btnNext = this.element.find('[data-btn-next]');
      this.$btnBack = this.element.find('[data-btn-back]');
      this.$btnDeactivate = this.element.find('[data-btn-deactivate]');
      this.$stepErrorMsg = this.element.find('.step-error-msg');

      this.$btnAddCard = this.element.find('[data-add-card]');
      this.$cardGroup = this.element.find('[data-card-group]');
      this.$cardList = this.element.find('[data-card-list]');
      this.$cardForm = this.element.find('[data-card-form]');
      this.$cardDesc = this.element.find('[data-card-desc]');
      this.$cardTotal = this.element.find('[data-card-total]');
      this.$formFootnote = this.element.find('[data-form-footnote]');

      this.$modalCoupon = $('#accountPlanCouponCode');
      this.$btnCallCoupon = this.element.find('[data-target="#accountPlanCouponCode"]');
      this.$footer = this.element.find('[data-account-plan-footer]');

      this.arrDnone = this.element.find('.' + hideClass + '');
      this.arrInvisible = this.element.find('.' + invisibleClass + '');

      this.$btnSubmitFeedback = this.element.find('[data-btn-submit-feedback]');
      this.$btnSubmitVoting = this.element.find('[data-btn-submit-voting]');
      this.$btnClaimOffer = this.element.find('[data-btn-claim-offer]');

      this.$step3Description = this.element.find('[data-description-step-3]');
      this.$step4Description = this.element.find('[data-description-step-4]');

      this.$stepDeactivate = this.element.find('[data-account-plan-deactivate]');
      this.$stepSuccess = this.element.find('[data-account-plan-deactivate-success]');
      this.$stepComplete = this.element.find('[data-account-plan-deactivate-complete]');
      this.$stepError = this.element.find('[data-account-plan-deactivate-error]');
      this.$checkoutError = this.element.find('[data-checkout-error]');

      this.$zendeskConfirm = this.element.find('[data-account-plan-deactivate-zendesk]');
      this.$zendeskCancelBtn = this.element.find('[data-zendesk-cancel-btn]');
      this.$zendeskRedirectBtn = this.element.find('[data-btn-zendesk-redirect]');

      this.$btnDeactivateReason = this.element.find('[data-btn-deactivate-reason]');
      this.$deactivateReasonRadios = this.element.find('[data-deactivate-reason-radio]');

      this.number = 0;

      this.initEvents();
    },
    initEvents: function () {
      var $that = this;

      $that.element.on('show.bs.modal', function (e) {
        $that.showPlanItemEvent(e);
      });

      $that.element.on('hidden.bs.modal', function () {
        isDeactivateFlow = false;
        isFreeTrialFlow = false;
        $that.$cardWrapper.removeClass(hideClass);
        $that.element.find('.account-plan-free-trial-wrapper').remove();
        $that.$form.find('input[name="is_free_trial_flow"]').val(0);

        if (refreshPage) {
          $that.showLoadingImg();
          location.reload();
        } else {
          $that.resetCheckoutForm();
        }
      });

      $that.$modalCoupon.on('hidden.bs.modal', function () {
          if ($('.modal.show').length) {
              $('body').addClass('modal-open');
          }
      });

      $that.$form.find('input, textarea').on('input', function () {
        $that.$formErrorMsg.html('');
        $that.$stepErrorMsg.html('');
      });

      $that.$planListItem.off(click).on(click, function () {
        $that.planListItemEvent($(this));
      });

      $that.$modalCoupon.off('coupon-code-apply.' + pluginName)
        .on('coupon-code-apply.' + pluginName, function (e, res) {
          $that.applyPromoCodeEvent(res);
        });

      $that.$btnAddCard.off(click).on(click, function () {
        $that.toggleCreditCardForm(true, false);
      });

      $that.$btnNext.off(click).on(click, function () {
        $that.nextButtonEvent($(this));
      });

      $that.$btnBack.off(click).on(click, function () {
        $that.backButtonEvent();
      });

      $that.$btnDeactivate.off(click).on(click, function (e) {
        e.preventDefault();

        var $btn = $(this),
            data = $btn.data();

        isDeactivateFlow = true;
        data.subscriptionId = currentSubscriptionId;

        if (typeof data.btnStep !== 'undefined' && parseInt(data.btnStep) !== 1) {
          $that.deactivateEvent($btn);
        } else {
          $that.getSubscriptionDetail(data, function (resData) {
            $that.$btnDeactivateReason.data('on-save-offer', resData.onSaveOffer);
            $that.deactivateEvent($btn);
          });
        }
      });

      $that.$zendeskCancelBtn.off(click).on(click, function (e) {
        e.preventDefault();
        $that.deactivateEvent($(this));
      });

      $that.$zendeskRedirectBtn.off(click).on(click, function (e) {
        e.preventDefault();
        window.location.href = $(this).data('zendesk-url');
      });

      this.$btnDeactivateReason.off(click).on(click, function (e) {
        e.preventDefault();

        isDeactivateFlow = true;
        $that.deactivateReason($(this));
      });

      $that.$btnClaimOffer.off(click).on(click, function (e) {
        e.preventDefault();

        $that.claimOfferEvent($(this));
      });

      $that.$btnSubmitFeedback.off(click).on(click, function (e) {
        e.preventDefault();

        $that.submitCancellationFeedbackEvent($(this));
      });

      $that.$btnSubmitVoting.off(click).on(click, function (e) {
        e.preventDefault();

        $that.element.modal('hide'); // Hide and reload page for now
      });

      $that.changeStep($that.$accountSubscriptionList, 0);
      $that.changeStep($that.$stepDeactivate);
    },
    showPlanItemEvent: function (e) {
      var
        $that = this,
        $target = $(e.relatedTarget),
        planType = $target.data('plan-type'),
        subscriptionId = $target.data('subscription-id'),
        checkoutOnly = $target.data('checkout-only'),
        editBtnPlan = $target.attr('data-edit-btn-plan'),
        $planItems = $('[data-item-plan-type="' + planType + '"]');

      $that.$checkoutError.addClass(hideClass);
      $that.$formErrorMsg.removeClass('text-center');
      $('.promo-code-label').remove();
      isPromoFlow = false;
      if (typeof checkoutOnly !== 'undefined' && checkoutOnly) {
        $that.renderCheckoutFormWithPromoIsApplied($target.data());
        $target.remove();
        return;
      }

      $that.$btnNext.text(planType === 'one-time' ? 'Next' : 'Change');

      if (planType === 'one-time') {
        var purchasedPlans = $target.data('purchased-plans').split(','),
          count = 0;

        $planItems.each(function (idx, el) {
          var $el = $(el),
            planCode = $el.data('item-plan-code');

          if (typeof planCode !== 'undefined' && $.inArray(planCode, purchasedPlans) === -1) {
            $el.removeClass(hideClass);
            count++;
          }
        });

        if (count === 0) {
          $('.no-one-time-available-message').removeClass(hideClass);
          $that.$btnNext.prop('disabled', true);
        } else {
          $planItems.not('.d-none').find('input').first().trigger('click');
        }
      } else {
        $planItems.removeClass(hideClass);
        if (subscriptionId) {
          $that.$deactivateSubscription.html('');

          var planCode = $target.data('plan-code'),
            isDeactivateOnly = $target.data('deactivate-only');

          if (isDeactivateOnly) {
            var planName = $target.text(),
              planDesc = $target.data('plan-desc'),
              subsPrice = $target.data('price');
            $that.renderDeactivateSubscriptionOnlyItem(planCode, planName, subsPrice, planDesc);
          }

          var $currentPlan = $('[data-item-plan-type="' + planType + '"][data-item-plan-code="' + planCode + '"]');

          checkoutPlan = planCode;
          checkoutPlanType = planType;
          currentSubscriptionId = subscriptionId;

          $currentPlan.addClass('current-plan');
          $currentPlan.find('.account-plan-select-item-status').text('Current Plan')
            .removeClass('account-plan-select-item-status--pink');
          $that.$modalCoupon.find('input[name="subscription_id"]').val(subscriptionId);

          $that.$btnNext.prop('disabled', true);
        } else {
          $that.$btnNext.text('Next');
        }

        if (typeof editBtnPlan !== 'undefined' && editBtnPlan) {
          $('input[name="select_plan_code"][value="' + editBtnPlan + '"]').trigger('click');
        } else {
          $planItems.not('.d-none, .current-plan').find('input').first().trigger('click');
        }
      }
    },
    renderDeactivateSubscriptionOnlyItem: function (planCode, planName, subsPrice, planDesc) {
      this.$deactivateSubscription
        .attr('data-item-plan-code', planCode)
        .html(
          '<label class="sr-only" for="_plan_recurring_0"></label>' +
          '<input type="radio" name="select_plan_code" id="_plan_recurring_0" value="' + planCode + '">' +
          '<div class="account-plan-select-item-wrapper d-block d-md-flex">' +
          '  <div class="account-plan-select-item-info">' +
          '    <p class="account-plan-select-item-status">Current Plan</p>' +
          '    <p class="account-plan-select-item-title">' + planName + '</p>' +
          '    <div class="account-plan-select-item-desc">' + planDesc + '</div>' +
          '  </div>' +
          '  <p class="account-plan-select-item-price" data-select-price>' + subsPrice + '</p>' +
          '</div>'
        );
    },
    planListItemEvent: function ($el) {
      var $that = this,
        isCurrent = $el.hasClass('current-plan');

      if ($el.hasClass(activeClass)) {
        return;
      }

      if (isCurrent) {
        $that.$modalCoupon.find('input[name="plan_code"]').val('');

        $el.find('.account-plan-select-item-status').text('Current Plan')
          .removeClass('account-plan-select-item-status--pink');
      } else {
        checkoutPlan = $el.data('item-plan-code');
        checkoutPlanType = $el.data('item-plan-type');
        $that.$modalCoupon.find('input[name="subscription_id"]').val(currentSubscriptionId);
      }

      $that.$form.find('input[name="plan_code"]').val(checkoutPlan);
      $that.$modalCoupon.find('input[name="plan_code"]').val(checkoutPlan);
      $that.$btnDeactivate.toggleClass(hideClass, !isCurrent);
      $that.$btnNext.prop('disabled', isCurrent);
      $that.$planListItem.removeClass(activeClass);
      $el.addClass(activeClass);
    },
    validateCheckoutEvent: function (promoCode, failedCallback, successCallback) {
      var $that = this;

      $that.showLoadingImg();

      $.fn.callAjaxGlobal({
        url: this.options.url,
        method: 'POST',
        cache: false,
        loader: false,
        data: {
          action: 'yah__validate_checkout',
          planCode: checkoutPlan,
          planType: checkoutPlanType,
          promoCode: promoCode,
          formCheck: formCheck,
          subscriptionId: currentSubscriptionId,
        }
      }).done(function (res) {
        if (!res.success) {
          $that.$formErrorMsg.html(res.data.message);

          if (typeof failedCallback === 'function') {
            failedCallback();
          }

          return;
        }

        $that.$headerPlan.addClass(invisibleClass);
        $that.$btnBack.removeClass(invisibleClass);
        $that.$promoGroup.removeClass(hideClass);

        var resData = res.data;

        $that.$modalCoupon.find('input[name="checkout_case"]').val(resData.checkoutCase);
        $that.$form.find('input[name="checkout_case"]').val(resData.checkoutCase);
        $that.$form.find('input[name="plan_type"]').val(checkoutPlanType);

        $that.$accountSubscriptionList.find('.plan-display-name').html(resData.planTitle);
        $that.$accountSubscriptionList.find('.plan-display-subheadline').html(resData.planSubheadline);

        $that.$cardDesc.html(resData.cartDescription);
        $that.$cardTotal.html(resData.cartDetail);

        if (resData.ccType !== 'undefined' && resData.ccLastFour !== null) {
          $that.$cardList.find('[data-card-group]').html(
            '<div class="form-radio">' +
            '  <div class="form-group-wrap">' +
            '    <label class="form-option">' +
            '      <span class="card-tag"><i class="ico ico-cards ico-card-' + resData.ccType + '"></i>' +
            '        ' + resData.ccLastFour + '</span>' +
            '      <input type="radio" name="use_cc_on_file" value="1" checked="">' +
            '      <span class="checkedbox"></span>' +
            '    </label>' +
            '  </div>' +
            '</div>'
          );
        }

        cartFootnote = typeof resData.footnote !== 'undefined' && resData.footnote ? resData.footnote : '';

        if (typeof resData.checkoutCase !== 'undefined' && parseInt(resData.checkoutCase) === 3) {
          $that.$btnCallCoupon.hide();
          $that.$btnNext.text('Confirm');
        } else {
          $that.$btnCallCoupon.show();
          $that.$btnNext.text('Purchase');
        }

        if (resData.payNow === 0) {
          $that.$btnCallCoupon.hide();
        }

        if (resData.isPromoFlow) {
          if (cartFootnote) {
            $that.$formFootnote.html(cartFootnote).show();
          }

          if (resData.promoCode) {
            var promoInput = '';
            if ((typeof resData.isFreeTrial !== 'undefined' && parseInt(resData.isFreeTrial) === 1) ||
                typeof resData.checkoutCase === 'undefined' || parseInt(resData.checkoutCase) !== 3) {
              promoInput = '<input type="hidden" name="couponCode" value="' + resData.promoCode + '">';
            }

            $that.$btnCallCoupon.hide().after('<p class="promo-code-label">' + promoInput + '<span class="promo-code-text">' + resData.promoCode + '</span></p>');
          }

          currentSubscriptionId = resData.subscriptionId;
          isPromoFlow = true;
        } else {
          isPromoFlow = false;
        }

        $that.renderCreditCard();

        $that.hideLoadingImg();

        if (typeof successCallback === 'function') {
          successCallback(res);
        }
      }).fail(function (request, status, error) {
        $that.hideLoadingImg();
        $that.$formErrorMsg.html('Something went wrong. Please refresh this page and try again.');

        if (typeof failedCallback === 'function') {
          failedCallback();
        }
      });
    },
    renderCreditCard: function () {
      var listCard = this.$cardList.find('input[type="radio"]');

      if (listCard.length) {
        this.toggleCreditCardForm(false, true);
      } else {
        this.toggleCreditCardForm(true, false);
      }
    },
    toggleCreditCardForm: function (isList, isForm) {
      this.$cardList.toggleClass(hideClass, isList);
      this.$cardForm.toggleClass(hideClass, isForm);

      this.$cardList.find('input[name="use_cc_on_file"]').prop('checked', !isList);
    },
    checkoutPlanEvent: function (data, failedCallback) {
      var $that = this;

      $that.showLoadingImg();

      data.push(
        {name: 'action', value: 'yah__checkout_plan'},
        {name: 'formCheck', value: formCheck},
        {name: 'subscription_id', value: currentSubscriptionId},
        {name: 'isPromoFlow', value: isPromoFlow ? 1 : 0}
      );

      $.fn.callAjaxGlobal({
        url: this.options.url,
        method: 'POST',
        cache: false,
        loader: false,
        data: $.param(data),
      }).done(function (res) {
        if (!res.success) {
          $that.$formErrorMsg.html(res.data.message);

          if (typeof failedCallback === 'function') {
            failedCallback();
          }

          return;
        }

        refreshPage = true;

        $that.$headerPlan.addClass(invisibleClass);
        $that.$btnBack.addClass(invisibleClass);
        $that.$promoGroup.addClass(hideClass);
        $that.$btnNext.addClass(hideClass);
        $that.$formFootnote.html('').hide();

        $that.hideLoadingImg();
      }).fail(function (request, status, error) {
        $that.hideLoadingImg();
        $that.$formErrorMsg.html('Something went wrong. Please refresh this page and try again.');

        if (typeof failedCallback === 'function') {
          failedCallback();
        }
      });
    },
    applyPromoCodeEvent: function (data) {
      var $that = this,
        $el = this.element,
        $btnCoupon = this.$btnCallCoupon,
        $codeLabel = $('<p class="promo-code-label"></p>'),
        promoCode = data.promo_code,
        planData = data.plan_data;

      beforeRedeemData = {
        title: $that.$accountSubscriptionList.find('.plan-display-name').html(),
        desc: $that.$accountSubscriptionList.find('.plan-display-subheadline').html(),
        totalDetail: $that.$cardTotal.html(),
        noCcRequired: typeof data.noccrequired !== 'undefined' && data.noccrequired,
        useCcOnFile: $that.$cardList.find('input[name="use_cc_on_file"]').is(":checked")
      };

      $codeLabel.append(
        '<input type="hidden" name="couponCode" value="' + promoCode + '">' +
        '<span class="promo-code-text">' + promoCode + '</span>' +
        '<span class="icon-close"></span>'
      );

      $btnCoupon.hide().after($codeLabel);
      $that.$cardTotal.find('.pay-now span').html(data.payNow);
      $that.$modalCoupon.modal('hide');
      if (typeof data.discount !== 'undefined') {
        $that.$cardTotal.find('.total-detail span').html(data.credit);
        $that.$cardTotal.find('.total-detail').append('<p>- Discount: ' + data.discount + '</p>');

        if (cartFootnote !== '') {
          $that.$cardTotal.find('.account-discount').remove();
          $that.$formFootnote.html(cartFootnote).show();
        }
      } else {
        $that.$cardTotal.append('<div class="total-detail">promo applied!</span>');
      }

      if (typeof data.subtotal !== 'undefined') {
        $that.$cardTotal.find('span.mid-month-price').html(data.subtotal);
      }

      $that.prepareFreeTrialFlow(data);

      $el.find('.icon-close').on(click, function () {
        $that.$cardDesc.removeClass(hideClass);

        if (beforeRedeemData.noCcRequired) {
          $that.$cardList.removeClass(hideClass);
          $that.$cardForm.addClass(hideClass);
          $that.$formErrorMsg.html('');
        }
        beforeRedeemData.noCcRequired = false;

        $that.$accountSubscriptionList.find('.plan-display-name').html(beforeRedeemData.title);
        $that.$accountSubscriptionList.find('.plan-display-subheadline').html(beforeRedeemData.desc);
        $that.$cardTotal.html(beforeRedeemData.totalDetail).removeClass(hideClass);
        $that.$form.find('input[name="plan_code"]').val(checkoutPlan);
        $that.$formFootnote.html('').hide();

        if (beforeRedeemData.useCcOnFile ||
          (typeof data.noccrequired !== 'undefined' && data.noccrequired)) {
          $that.$cardList.find('input[name="use_cc_on_file"]').prop('checked', true);
        }

        $codeLabel.remove();
        $btnCoupon.show();
      });

      if (beforeRedeemData.noCcRequired) {
        // Update title/desc/plan code
        $that.$accountSubscriptionList.find('.plan-display-name').html(planData.name);
        $that.$accountSubscriptionList.find('.plan-display-subheadline').html(planData.desc);
        $that.$form.find('input[name="plan_code"]').val(data.plan_code);
        $that.$cardList.find('input[name="use_cc_on_file"]').prop('checked', false);
        $that.$cardList.addClass(hideClass);
        $that.$cardForm.addClass(hideClass);
        $that.$cardDesc.addClass(hideClass);
        $that.$cardTotal.addClass(hideClass);
        $that.$formFootnote.html('').hide();

        $that.resetCard();
      }
    },
    renderCheckoutFormWithPromoIsApplied: function (data) {
      var $that = this,
        checkoutPromoCode = data.promoCode;

      checkoutPlan = data.planCode;
      checkoutPlanType = data.planType;
      formCheck = $that.$btnNext.data('form-check');
      currentSubscriptionId = null;

      $that.validateCheckoutEvent(checkoutPromoCode, function (res) {
        $that.$headerPlan.addClass(invisibleClass);
        $that.hideLoadingImg();
        $that.$btnNext.addClass(hideClass);
        $that.$checkoutError.removeClass(hideClass);
        $that.$formErrorMsg.addClass('text-center');
      }, function (res) {
        $that.$checkoutError.addClass(hideClass);
        $that.$formErrorMsg.removeClass('text-center');
        $that.$btnBack.addClass(invisibleClass);

        $that.number = 1;
        $that.changeStep($that.$accountSubscriptionList, $that.number);
        $that.$form.find('input[name="plan_code"]').val(checkoutPlan);

        $that.prepareFreeTrialFlow(res.data);
        $('body').addClass('modal-open');
      });
    },
    prepareFreeTrialFlow: function (resData) {
      var $that = this;

      if (typeof resData.isFreeTrial !== 'undefined' && parseInt(resData.isFreeTrial) === 1) {
        isFreeTrialFlow = true;
        $that.$headerPlan.removeClass(invisibleClass);
        $that.$btnNext.text('Start Free Trial');
        $that.$btnBack.addClass(invisibleClass);

        $that.$cardWrapper.parent().append(
            '<div class="account-plan-free-trial-wrapper">' +
            '  <div class="account-plan-select-item">' +
            '    <input type="radio" name="free-trial-plan" id="free-trial-plan" value="' + resData.planCode + '">' +
            '    <div class="account-plan-select-item-wrapper d-block d-md-flex">' +
            '      <div class="account-plan-select-item-info">' +
            '        <p class="account-plan-select-item-status account-plan-select-item-status--pink"></p>' +
            '        <p class="account-plan-select-item-title">' + resData.planTitle + '</p>' +
            '        <div class="account-plan-select-item-desc">' + resData.planSubheadline + '</div>' +
            '      </div>' +
            '      <p class="account-plan-select-item-price">' + resData.planPrice + '</p>' +
            '    </div>' +
            '  </div>' +
            '</div>'
        );
        $that.$cardWrapper.addClass(hideClass);
        $that.element.find('#free-trial-plan').trigger('click');
        $that.$form.find('input[name="is_free_trial_flow"]').val(1);
        $that.$form.find('input[name="plan_code"]').val(resData.planCode);
        $that.element.find('.icon-close').remove();
      }
    },
    showLoadingImg: function () {
      this.$loading.removeClass(hideClass);
      this.$form.addClass(hideClass);
      this.$formErrorMsg.html('');
      this.$stepErrorMsg.html('');
    },
    hideLoadingImg: function () {
      this.$loading.addClass(hideClass);
      this.$form.removeClass(hideClass);
    },
    nextButtonEvent: function ($el) {
      var $that = this;

      $that.number += 1;
      formCheck = $el.data('form-check');

      if (!isFreeTrialFlow) {
        $that.$btnNext.text('Purchase');
      }

      if ($that.number === 1) {
        $that.validateCheckoutEvent(null, function () {
          $that.number -= 1;
          $that.changeStep($that.$accountSubscriptionList, $that.number);
          $that.hideLoadingImg();
          $that.$btnNext.text(checkoutPlanType === 'one-time' || currentSubscriptionId === null ? 'Next' : 'Change');
        });
      } else if ($that.number === 2 && $that.$cardForm.is(':visible')) {
        if (!$that.$form['form-validate']('handleValidate').valid()) {
          $that.number -= 1;
          return;
        }
      }

      if ($that.number === ($that.$accountSubscriptionList.length - 1)) {
        var data = $that.$form.serializeArray();

        $that.checkoutPlanEvent(data, function () {
          $that.number -= 1;
          $that.changeStep($that.$accountSubscriptionList, $that.number);
          $that.hideLoadingImg();
        });
      }

      $that.changeStep($that.$accountSubscriptionList, $that.number);
    },
    backButtonEvent: function () {
      var $that = this;

      $that.$zendeskConfirm.addClass(hideClass);
      if (isDeactivateFlow) {
        if (currentDeactivateStep === 4 || currentDeactivateStep === 3) {
          currentDeactivateStep--;
          $that.changeDeactivateStep(currentDeactivateStep);
        } else {
          $that.number = 0;
          $that.changeStep($that.$accountSubscriptionList, $that.number);

          $that.$footer.removeClass(hideClass);
          $that.$headerPlan.removeClass(invisibleClass);
          $that.$stepDeactivate.addClass(hideClass);
          $that.$btnBack.addClass(invisibleClass);
        }
      } else {
        $that.number -= 1;
        $that.$formErrorMsg.html('');
        $that.$stepErrorMsg.html('');
        $that.$promoGroup.find('.icon-close').trigger(click);

        if ($that.number < 1) {
          $that.$headerPlan.removeClass(invisibleClass);
          $that.$btnBack.addClass(invisibleClass);
          $that.$promoGroup.addClass(hideClass);
          $that.$btnNext.text(checkoutPlanType === 'one-time' || currentSubscriptionId === null ? 'Next' : 'Change');
          $that.resetCard();
        }

        $that.changeStep($that.$accountSubscriptionList, $that.number);
      }
    },
    changeStep: function ($stepContent, index) {
      $stepContent.addClass(hideClass);
      $stepContent.eq(index).removeClass(hideClass);
    },
    resetCheckoutForm: function () {
      //reset element default has class dnone, inivisble
      this.element.find('.' + hideClass + '').removeClass(hideClass);
      this.element.find('.' + invisibleClass + '').removeClass(invisibleClass);
      this.element.find('.' + activeClass + '').removeClass(activeClass);
      this.arrDnone.addClass(hideClass);
      this.arrInvisible.addClass(invisibleClass);
      this.$formErrorMsg.html('');
      this.$stepErrorMsg.html('');
      this.$formFootnote.html('').hide();

      //reset card form
      this.resetCard();

      //reset coupon
      this.element.find('.icon-close').triggerHandler('click');

      //reset select plan
      this.$planListItem.filter('[data-select-current="1"]').last().triggerHandler('click');

      //reset step
      this.number = 0;
      this.changeStep(this.$accountSubscriptionList, this.number);
      this.changeStep(this.$stepDeactivate);
    },
    resetCard: function () {
      this.$form.find('label.error').html('');
      this.$cardForm.find('input').val('');
      this.$cardForm.find('.ico.ico-cards').removeClass().addClass('ico ico-cards');
      this.element.find('.validate-error').hide();
    },
    getSubscriptionDetail: function (data, callback) {
      var $that = this;

      $that.showLoadingImg();
      $that.$stepErrorMsg.html('');

      $.fn.callAjaxGlobal({
        url: $that.options.url,
        method: 'POST',
        cache: false,
        loader: false,
        data: data,
      }).done(function (res) {
        $that.hideLoadingImg();
        if (typeof callback === 'function') {
          callback(res.data);
        }
      }).fail(function (request, status, error) {
      });
    },
    deactivateEvent: function ($btn) {
      var $that = this,
        stepNum = $btn.data('btn-step');

      currentDeactivateStep = stepNum;
      $that.$zendeskConfirm.addClass(hideClass);

      if (typeof stepNum === 'undefined' || stepNum === '') {
        $that.cancelSubscriptionEvent($btn);
      } else if (parseInt(stepNum) === 1 || parseInt(stepNum) === 2) {
        $that.$footer.addClass(hideClass);
        $that.$headerPlan.addClass(invisibleClass);
        $that.$accountSubscriptionList.addClass(hideClass);
        $that.$btnBack.removeClass(invisibleClass);
        $that.changeDeactivateStep(stepNum);
      } else if (parseInt(stepNum) === 3) {
        var data = $btn.data();
        data.subscriptionId = currentSubscriptionId;
        data.selectedReason = initialCancellationReason;

        $that.getCancellationFlowData(data, function (res) {
          if (res.success) {
            if (typeof res.data.showOffer !== 'undefined' && res.data.showOffer) {
              planList = res.data;
              $that.renderSelectList();

              // Check if step 3 is empty
              if (typeof planList.currentPlan !== 'undefined' && planList.currentPlan === null) {
                stepNum++;
                currentDeactivateStep = 3;
              }

              $that.changeDeactivateStep(stepNum);
            } else {
              refreshPage = true;
              $btn.closest('.account-plan-status').addClass(hideClass);
              $btn.closest('.account-plan-status').next().addClass(hideClass);
              $that.showStatusDeactivate(true, false, true);
            }
          } else {
            $that.$stepErrorMsg.html(res.data.message);
          }
        });
      } else if (parseInt(stepNum) === 4) {
        if (typeof planList.onDemandOffer !== 'undefined' && planList.onDemandOffer) {
          $that.changeDeactivateStep(stepNum);
        } else {
          $that.cancelSubscriptionEvent($btn);
        }
      }
    },
    deactivateReason: function ($btn) {
      var $that = this,
          $selectedReason = $that.$deactivateReasonRadios.find('input:checked'),
          reasonId = $selectedReason.data('reason-id'),
          onSaveOffer = $btn.data('on-save-offer');

      if (typeof reasonId === 'undefined') {
        location.reload();
      } else {
        initialCancellationReason = $selectedReason.parent('.radio-wrapper').find('span').text();
        reasonId = parseInt(reasonId);
        $that.$btnBack.addClass(invisibleClass);

        if (reasonId === 1 || reasonId === 3 || reasonId === 4 || reasonId === 6 || reasonId === 7) {
          if (typeof onSaveOffer !== 'undefined' && parseInt(onSaveOffer) === 1) {
            $that.cancelSubscriptionEvent($btn, {
              action: $btn.data('cancel-action'),
              formCheck: $btn.data('cancel-form-check'),
            });
          } else {
            $that.deactivateEvent($btn);
          }
        } else if (reasonId === 2 || reasonId === 5) {
          $that.element.find('.account-plan-status').addClass(hideClass);
          $that.$zendeskConfirm.removeClass(hideClass);
        } else { // 8
          currentDeactivateStep = 2;
          $that.changeDeactivateStep(2);
        }
      }
    },
    changeDeactivateStep: function (stepNumber) {
      this.$stepDeactivate.addClass(hideClass);
      this.$stepDeactivate.parent().find('[data-step="' + stepNumber + '"]').removeClass(hideClass);
    },
    submitCancellationFeedbackEvent: function ($btn) {
      var $that = this,
        $formBlock = $btn.closest('.account-plan-status'),
        data = $btn.data(),
        feedback = $formBlock.find('textarea[name="feedback-message"]').val();

      if (parseInt(data.isFeedbackOnly) === 1 && feedback.length < 1) {
        $that.$stepErrorMsg.html('Please fill in the details to submit your ticket!');
        return;
      }

      data.feedbackMessage = feedback;
      data.selectedReason = initialCancellationReason;
      data.subscriptionId = currentSubscriptionId;
      $that.submitCancellationFeedback(data, function (res) {
        if (typeof res.isFeedbackOnly !== 'undefined' && res.isFeedbackOnly) {
          $formBlock.addClass(hideClass);
          $that.showStatusDeactivate(false, true, true);
        } else {
          $that.element.modal('hide');
        }
      });
    },
    submitCancellationFeedback: function (data, callback) {
      var $that = this;

      $that.showLoadingImg();
      $that.$stepErrorMsg.html('');

      $.fn.callAjaxGlobal({
        url: $that.options.url,
        method: 'POST',
        cache: false,
        loader: false,
        data: data,
      }).done(function (res) {
        refreshPage = true;
        $that.hideLoadingImg();

        if (typeof callback === 'function') {
          callback(res.data);
        }
      }).fail(function (request, status, error) {
      });
    },
    claimOfferEvent: function ($btn) {
      var $that = this,
        offerData = $btn.data(),
        $stepBlock = $btn.closest('.account-plan-select').find('input[type="radio"]:checked');

      offerData.subscriptionId = currentSubscriptionId;
      offerData.planCode = $stepBlock.val();
      offerData.promoCode = $stepBlock.data('promo-code');
      offerData.selectedReason = initialCancellationReason;

      $that.claimOffer(offerData, function (res) {
        if (res.success) {
          refreshPage = true;
          $that.$stepSuccess.removeClass(hideClass);
        } else {
          $that.$stepError.removeClass(hideClass);
          $that.$stepError.find('.deactivate-error-msg').html(res.data.message);
        }

        $that.$btnBack.addClass(invisibleClass);
      });
    },
    claimOffer: function (data, callback) {
      var $that = this;

      $that.showLoadingImg();
      $that.$stepErrorMsg.html('');
      $that.changeDeactivateStep(-1);

      $.fn.callAjaxGlobal({
        url: $that.options.url,
        method: 'POST',
        cache: false,
        loader: false,
        data: data,
      }).done(function (res) {
        $that.hideLoadingImg();

        if (typeof callback === 'function') {
          callback(res);
        }
      }).fail(function (request, status, error) {
        $that.hideLoadingImg();
        $that.$stepError.removeClass(hideClass);
        $that.$stepError.find('.deactivate-error-msg')
          .html('Something went wrong! Please refresh this page and try again.');
        $that.$btnBack.addClass(invisibleClass);
      });
    },
    getCancellationFlowData: function (data, callback) {
      var $that = this;

      $that.showLoadingImg();
      $that.$stepErrorMsg.html('');

      $.fn.callAjaxGlobal({
        url: $that.options.url,
        method: 'GET',
        cache: false,
        loader: false,
        data: data,
      }).done(function (res) {
        $that.hideLoadingImg();

        if (typeof callback === 'function') {
          callback(res);
        }
      }).fail(function (request, status, error) {
      });
    },
    cancelSubscriptionEvent: function ($btn, overrideData) {
      var $that = this,
        cancelData = $btn.data();

      if (typeof overrideData !== 'undefined' && overrideData) {
        cancelData = overrideData;
      }

      cancelData.subscriptionId = currentSubscriptionId;
      cancelData.selectedReason = initialCancellationReason;
      $that.cancelSubscription(cancelData, function (res) {
        if (res.success) {
          refreshPage = true;
          $that.changeDeactivateStep(-1);
          $that.$stepComplete.removeClass(hideClass);
          $that.$btnBack.addClass(invisibleClass);
        } else {
          $that.$stepErrorMsg.html(res.data.message);
        }
      });
    },
    cancelSubscription: function (data, callback) {
      var $that = this;

      $that.showLoadingImg();
      $that.$stepErrorMsg.html('');

      $.fn.callAjaxGlobal({
        url: $that.options.url,
        method: 'POST',
        cache: false,
        loader: false,
        data: data,
      }).done(function (res) {
        $that.hideLoadingImg();

        if (typeof callback === 'function') {
          callback(res);
        }
      }).fail(function (request, status, error) {
      });
    },
    showStatusDeactivate: function (isSuccess, isComplete, isError) {
      this.$stepSuccess.toggleClass(hideClass, isSuccess);
      this.$stepComplete.toggleClass(hideClass, isComplete);
      this.$stepError.toggleClass(hideClass, isError);
      this.$btnBack.addClass(invisibleClass);
    },
    renderSelectList: function () {
      var
        $that = this,
        $recurringOffer = $that.element.find('[data-recurring-offer]'),
        $ondemandOffer = $that.element.find('[data-on-demand-offer]'),
        planData = typeof planList !== 'undefined' && planList ? planList : {},
        renderPlanItem = function (item, isStep3) {
          if (typeof item !== 'undefined' && item && typeof item.planCode !== 'undefined') {
            var inputName = isStep3 ? 'recurring_plan_code' : 'ondemand_plan_code';

            if (typeof item.planSubDesc !== 'undefined' && item.planSubDesc) {
              if (isStep3) {
                $that.$step3Description.html(item.planSubDesc);
              } else {
                $that.$step4Description.html(item.planSubDesc);
              }
            }

            return '<div class="account-plan-select-item">' +
              '  <input type="radio" name="' + inputName + '" value="' + item.planCode + '" data-promo-code="' + item.promoCode + '">' +
              '  <div class="account-plan-select-item-wrapper d-block d-md-flex">' +
              '    <div class="account-plan-select-item-info">' +
              '      <span class="account-plan-select-item-status  account-plan-select-item-status--pink">' + item.planSubHeadline + '</span>' +
              '      <p class="account-plan-select-item-title">' + item.planName + '</p>' +
              '      <div>' + item.planDesc + '</div>' +
              '    </div>' +
              '    <p class="account-plan-select-item-price" data-select-price>' + item.planPrice + '</p>' +
              '  </div>' +
              '</div>';
          } else {
            return '';
          }
        };

      $recurringOffer.html(renderPlanItem(planData.currentPlan, true));
      $recurringOffer.find('input:radio').prop('checked', true);
      $ondemandOffer.html(renderPlanItem(planData.onDemandOffer, false));
      $ondemandOffer.find('input:radio').prop('checked', true);
    },
    destroy: function () {
      $.removeData(this.element[0], pluginName);
    }
  };

  $.fn[pluginName] = function (options, params) {
    return this.each(function () {
      var instance = $.data(this, pluginName);
      if (!instance) {
        $.data(this, pluginName, new Plugin(this, options));
      } else if (instance[options]) {
        instance[options](params);
      }
    });
  };

  $.fn[pluginName].defaults = {
    url: yahAjax.ajaxurl
  };

  $(function () {
    $('[data-' + pluginName + ']')[pluginName]();
  });

}(jQuery, window));
