;(function ($, window, undefined) {
  'use strict';

  var pluginName = 'subscription-plan';

  function Plugin(element, options) {
      this.element = $(element);
      this.options = $.extend({}, $.fn[pluginName].defaults, this.element.data(), options);
      this.init();
  }

  Plugin.prototype = {
      init: function () {
        this.$input = this.element.find('[data-radiobox]');
        this.$btnPlanDetail = this.element.find('[data-btn-plan-detail]');
        // this.$btnUpdatePlan = this.element.find('[data-btn-update-plan]');

        this.handleEvent();
      },
      handleEvent: function () {
        var that = this,
            classPlanDetail = that.options.classPlanDetail,
            classDescParagraph = that.options.classDescParagraph,
            classWrapForm = that.options.classWrapForm,
            classHidden = that.options.classHidden;

        this.$input.on("change", function (e) {
          $(classPlanDetail).addClass(classHidden);
          $(classDescParagraph).addClass(classHidden);

          var $planDetailEle = $(this).closest(classWrapForm).find(classPlanDetail),
            subsId = $(this).data('subs-id') || '';

          $planDetailEle.removeClass(classHidden);
          that.element.find('input[name="subscription_id"]').val(subsId);
        });

        this.$btnPlanDetail.on("click", function (e) {
          $(this).closest(classWrapForm).find(classDescParagraph).removeClass(classHidden);
        });

        $('.modal-subscription-plan').on('hidden.bs.modal', function (e) {
          var $that = $(this);

          $that.find('.plan-detail').addClass('d-none');
          $that.find('.description-paragraph').addClass('d-none');
        });

        $('#subscriptionPlan').on('shown.bs.modal', function (e) {
          var $that = $(this),
            subsId = $that.find('input[name="subscription_id"]').val();

          if (typeof subsId !== 'undefined' && subsId) {
            $that.find('.deactivate-plan-group')
              .show()
              .find('input').attr('data-subs-id', subsId);
          }
        });

        // this.$btnUpdatePlan
        //   .off('click')
        //   .on('click', function (e) {
        //     that.handleUpdatePlan($(this));
        //     e.preventDefault();
        //   });
      },
      handleUpdatePlan: function (element) {
        var $form = element.parents('form'),
            formData = $form.serializeArray();
        $form.find('.text-danger').remove();

        $.ajax({
          type: "POST",
          url: yahAjax.ajaxurl,
          data: formData,
          cache: false,
          success: function (res) {
              if(res.success) {
                  $('.modal').modal('hide');
              }
              else {
                  console.log("Fail scenario");
              }
          },
          error: function (err) {
            console.log(err);

            var errMess = '<label class="text-danger">' + err.statusText + '</label>';
            element.parents('form').append(errMess);
          }
        });
      },
      destroy: function () {
          $.removeData(this.element[0], pluginName);
      }
  };

  $.fn[pluginName] = function (options, params) {
      return this.each(function () {
          var instance = $.data(this, pluginName);
          if (!instance) {
              $.data(this, pluginName, new Plugin(this, options));
          } else if (instance[options]) {
              instance[options](params);
          }
      });
  };

  $.fn[pluginName].defaults = {
    classPlanDetail: '.plan-detail',
    classDescParagraph: '.description-paragraph',
    classWrapForm: '.form-group-wrap',
    classHidden: 'd-none'
  };

  $(function () {
      $('[data-' + pluginName + ']')[pluginName]();
  });

}(jQuery, window));
