;(function ($, window, undefined) {
  'use strict';

  var pluginName = 'profile';

  function Plugin(element, options) {
      this.element = $(element);
      this.options = $.extend({}, $.fn[pluginName].defaults, this.element.data(), options);
      this.init();
  }

  Plugin.prototype = {
      init: function () {
          this.cacheDOM();
          this.handleEvent();
      },

      cacheDOM: function () {
          this.input = this.element.find('.form-control');
          this.btnEditProfile = this.element.find('.btn-edit-profile');
          this.btnDeleteProfile = this.element.find('.btn-delete-avatar');
      },

      handleEvent: function () {
        this.input.on('input', function() {
          var $target = $(this);

          if ($target.data('text-content') != $target.val()) {
            $target.siblings('.btn-small').removeClass('d-none');
            $target.addClass('text-change');
          } else {
            $target.siblings('.btn-small').addClass('d-none');
            $target.removeClass('text-change');
          }
        });

        this.input.on('blur', function() {
          var $target = $(this);

          $target.scrollTop(0);
        });
      },
      destroy: function () {
          $.removeData(this.element[0], pluginName);
      }
  };

  $.fn[pluginName] = function (options, params) {
      return this.each(function () {
          var instance = $.data(this, pluginName);
          if (!instance) {
              $.data(this, pluginName, new Plugin(this, options));
          } else if (instance[options]) {
              instance[options](params);
          }
      });
  };

  $.fn[pluginName].defaults = {};

  $(function () {
      $('[data-' + pluginName + ']')[pluginName]();
  });

}(jQuery, window));
