; (function ($, window) {
  'use strict';

  var pluginName = 'save-profile-validate',
    formGroupWrap = '.form-group-wrap',
    clssValidError = 'validate-error',
    click = 'click.' + pluginName,
    regexName = /^(?!\s)(?!.*\s$)[a-zA-Z0-9\s()-]+$/,
    regexEmail  = /^([a-zA-Z0-9_.+-])+\@(?!-)+(([a-zA-Z0-9-])+[a-z0-9]\.)+([a-zA-Z0-9]{2,4})+$/,
    regexEmail2 = /^([a-zA-Z0-9_.-])+\@(?!-)+(([a-zA-Z0-9-])+[a-z0-9]\.)+([a-zA-Z0-9]{2,4})+$/;


  function Plugin(element, options) {
    this.element = $(element);
    this.options = $.extend({}, $.fn[pluginName].defaults, this.element.data(), options);
    this.init();
  }

  Plugin.prototype = {
    init: function () {
      this.cacheDOM();
      this.initSaveProfileValidate();
      this.handleEvent();
    },

    initSaveProfileValidate: function () {
      $.validator.addMethod("regexName", function (value, element) {
        return regexName.test(value);
      });

      $.validator.addMethod("regexEmail", function (value, element) {
        return typeof yahAjax.allowPlus !== 'undefined' && yahAjax.allowPlus ? regexEmail.test(value) : regexEmail2.test(value);
      });

      this.$form.validate({
        rules: {
          name: {
            required: true,
            regexName: true
          },
          email: {
            required: true,
            regexEmail: true
          },
          address: {
            required: true
          },
          postalcode: {
            required: true
          }
        },
        messages: {
          name: 'Please enter a valid name',
          email: 'Please enter a valid email address',
          address: 'Please enter an address',
          postalcode: 'Please enter a postal code',
        },
        errorPlacement: function (error, element) {
          (element.closest(formGroupWrap)).after(error.addClass(clssValidError));
        }
      });
    },

    cacheDOM: function () {
      this.$form = this.element;
      this.$btnSaveProfile = this.element.find('[data-save-profile]');
    },

    handleEvent: function () {
      var that = this;

      this.$btnSaveProfile
        .off(click)
        .on(click, function (e) {
          e.preventDefault();
          that.handleSaveProfileValidate($(this), function($element) {
            that.callAjaxSaveProfile($element);
          });
        });
    },

    handleSaveProfileValidate: function ($target, cb) {
      var $validationEle = $target.siblings('input, textarea'),
        isValid = this.$form.validate().element($validationEle);

      if (isValid && typeof cb === 'function') {
        cb($target);
      }
    },

    callAjaxSaveProfile: function($ele) {
      var $form  = $ele.closest("form");

      $.fn.callAjaxGlobal({
        method: "POST",
        url: yahAjax.ajaxurl,
        dataType: 'json',
        data: {
          "action": $form.find('input[name="wpAction"]').val(),
          "first_name": $form.find("#firstName").val(),
          "last_name": $form.find("#LastName").val(),
          "email_addr": $form.find("#email").val(),
          "formcheck": $form.find("input#checkForm").val()
        },
        loader: true
      }).done(function (res) {
        var $inputEle = $ele.siblings('.form-control');

        if (res.success) {
          $inputEle.data('text-content', $inputEle.val());
          $ele.addClass('d-none');
          $inputEle.removeClass('text-change');
        } else {
          var inputName = $inputEle.attr("name"),
            jsonValidate = "{\"" + inputName + "\":\"" + res.data.message + "\"}";
          $form.validate().showErrors(JSON.parse(jsonValidate));
        }

        $form.find('textarea').scrollTop(0);
      }).fail(function (request, status, error) {
      });
    },

    destroy: function () {
      $.removeData(this.element[0], pluginName);
    }
  };

  $.fn[pluginName] = function (options, params) {
    return this.each(function () {
      var instance = $.data(this, pluginName);
      if (!instance) {
        $.data(this, pluginName, new Plugin(this, options));
      } else if (instance[options]) {
        instance[options](params);
      }
    });
  };

  $.fn[pluginName].defaults = {
    method: 'POST',
    url: yahAjax.upcomingLiveClasses
  };

  $(function () {
    $('[data-' + pluginName + ']')[pluginName]();
  });

}(jQuery, window));
