;(function ($, window) {
  'use strict';

  var pluginName = 'comment',
    input = 'input.' + pluginName,
    click = 'click.' + pluginName,
    classActive = 'active',
    commentId = null,
    $primaryComment = null;

  function Plugin(element, options) {
    this.element = $(element);
    this.options = $.extend({}, $.fn[pluginName].defaults, this.element.data(), options);
    this.init();
  }

  Plugin.prototype = {
    init: function () {
      this.cacheDOM();
      this.renderCommentList();
      this.handleEvents();
      this.convertCommentDate();
    },

    cacheDOM: function () {
      this.$blockDetail = this.element.find('[data-comment-block-detail]');
      this.$commentItems = this.element.find('[data-comment-item]');
      this.$inputComment = this.element.find('[data-enter-comment]');
      this.$btnSave = this.element.find('[data-btn-save-comment]');
      this.$btnTotal = this.element.find('[data-total]');
      this.$modalReply = $('#reply-comment');
      this.$inputReply = this.$modalReply.find('[data-reply-content]');
      this.$btnReplyModal = this.$modalReply.find('[data-btn-submit]');

      this.totalComment = this.$commentItems.length;
    },

    handleEvents: function () {
      var that = this;

      this.element
        .on('click', '[data-comment-item] .btn-reply', function (e) {
          e.preventDefault();

          $primaryComment = $(this).closest('[data-comment-item]');
          commentId = $primaryComment.data('comment-id');
        });

      this.$modalReply.on('hidden.bs.modal', function () {
        that.$inputReply.val('');
        commentId = null;
      });

      this.$btnReplyModal
        .off(click)
        .on(click, function (e) {
          e.preventDefault();
          that.handleReplyComment();
        });

      this.$inputComment
        .off(input)
        .on(input, function () {
          var valueInput = $(this).val();

          that.$btnSave.toggleClass(classActive, valueInput.length > 0);
        });

      this.$btnSave
        .off(click)
        .on(click, function (e) {
          e.preventDefault();
          that.handleAddComment();
        });

      this.$btnTotal
        .off(click)
        .on(click, function (e) {
          e.preventDefault();
          that.showAllComments();
        });
    },

    handleReplyComment: function () {
      var that = this,
        valueReply = this.$inputReply.val();

      if (valueReply.length === 0) {
        return;
      }

      this.postComment(this.$inputReply, function (res) {
        that.renderComment(res, $primaryComment, 'append');
        that.$modalReply.modal('hide');
        that.updateHistoryState();
      });
    },

    handleAddComment: function () {
      var that = this;

      this.postComment(this.$inputComment, function (res) {
        that.renderComment(res, that.$blockDetail, 'prepend');
        that.$inputComment.val('').trigger(input);
        that.updateHistoryState();
      });
    },

    updateHistoryState: function() {
      history.pushState('', document.title);
    },

    postComment: function ($el, cb) {
      var
        that = this,
        videoId = $el.data('video-id'),
        comment = $.trim($el.val()),
        formCheck = $el.data('form-check'),
        inProgress = false;

      $el.val('');

      if (comment.length > 0 && !inProgress) {
        inProgress = true;

        $.fn.callAjaxGlobal({
          type: that.options.method,
          url: that.options.url,
          method: 'POST',
          data: {
            action: 'post_comment',
            videoId: videoId,
            commentId: commentId,
            comment: comment,
            formCheck: formCheck
          },
          cache: false,
          loader: true
        }).done(function (res) {
          if (typeof cb === 'function') {
            cb(res);
          }
          inProgress = false;
        }).fail(function (request, status, error) {
        });
      }
    },

    renderComment: function (res, $block, methodAdd) {
      if (res.success) {
        var
          commentData = res.data.commentData,
          isReply = $block[0].hasAttribute('data-comment-item'),
          htmlReplyBtn = '<a class="btn-reply" href="#" data-toggle="modal" data-target="#reply-comment">Reply</a>',
          htmlCommentContent =
            '<div class="comment-item-wrapper">' +
            ' <div class="comment-item-content">' + commentData.comment + '</div>' +
            ' <div class="comment-item-name"><p>' + commentData.name + '</p></div>' +
            ' <div class="comment-item-date"><p data-comment-date="' + commentData.date + '"></p></div>' +
            (isReply ? '' : htmlReplyBtn) +
            '</div>',
          htmlComment = ''
        ;

        if (isReply) {
          htmlComment =
            '<div class="comment-item-reply" style="display: none;" id="comment-' + commentData.commentId + '">' + htmlCommentContent + '</div>';

        } else {
          htmlComment =
            '<div class="comment-item" id="comment-' + commentData.commentId + '" data-comment-item' +
            '     data-comment-id="' + commentData.commentId + '">' + htmlCommentContent + '</div>';
        }

        $block[methodAdd](htmlComment);

        $('#comment-' + commentData.commentId).fadeIn(1000);

        this.convertCommentDate();
      } else {
        console.log("Error scenario here...");
      }
    },

    renderCommentList: function () {
      var min = Math.min(this.options.itemsToShow, this.totalComment);

      for (var i = 0; i < min; i++) {
        this.$commentItems.eq(i).addClass('d-block');
      }

      if ((this.options.itemsToShow >= this.totalComment) || this.totalComment.length === 0) {
        this.$btnTotal.hide();
      } else {
        var rest = parseInt(this.totalComment) - parseInt(this.options.itemsToShow);
        this.$btnTotal.children().text('All ' + rest + ' Comments');
      }
    },

    showAllComments: function () {
      var i = this.options.itemsToShow;

      for (; i < this.totalComment; i++) {
        this.$commentItems.eq(i).fadeIn(1000);
      }

      this.$btnTotal.fadeOut(500);
    },

    convertCommentDate: function () {
      var $that = this;

      $('[data-comment-date]').each(function () {
        var
          commentDate = $(this).data('comment-date'),
          userDate = moment(commentDate)
        ;

        $(this).html(userDate.format($that.options.dateFormat));
      });
    },

    destroy: function () {
      $.removeData(this.element[0], pluginName);
    }
  };

  $.fn[pluginName] = function (options, params) {
    return this.each(function () {
      var instance = $.data(this, pluginName);
      if (!instance) {
        $.data(this, pluginName, new Plugin(this, options));
      } else if (instance[options]) {
        instance[options](params);
      }
    });
  };

  $.fn[pluginName].defaults = {
    method: 'POST',
    itemsToShow: 3,
    dateFormat: 'MM/DD/YYYY',
    url: yahAjax.ajaxurl
  };

  $(function () {
    $('[data-' + pluginName + ']')[pluginName]();
  });

}(jQuery, window));
