;(function ($, window, undefined) {
  'use strict';

  var
    pluginName = 'journey-series',
    click = 'click.' + pluginName
  ;

  function Plugin(element, options) {
    this.element = $(element);
    this.options = $.extend({}, $.fn[pluginName].defaults, this.element.data(), options);

    this.init();
  }

  Plugin.prototype = {
    init: function () {
      var $that = this;

      $('[data-journey-series-video-item]').off(click).on(click, function (e) {
        e.preventDefault();

        var $item = $(this);

        $that.getFeaturedVideo($item.data('video-id'), $item.data('leg-id'));
      });

      $('.js-item-day-number').off(click).on(click, function () {
        var $btn = $(this);

        $that.updateDayWatched($btn.data('js-id'), $btn.data('js-day-watched'), $btn.data('form-check'));
      });
    },
    getFeaturedVideo: function (videoId, legId) {
      $.fn.callAjaxGlobal({
        url: yahAjax.ajaxurl,
        method: 'GET',
        data: {
          action: 'yah_js_get_video',
          videoId: videoId,
          formCheck: this.options.formCheck,
        },
        loader: true
      }).done(function (res) {
        if (res.success) {
          var $featuredVideo = $('[data-journey-series-featured-video]');

          $featuredVideo.html(res.data.html);

          // Init other plugins
          $(window).trigger('scrollmagic:change');
          $('[data-toggle-action]')['toggle-action']('init');
          $.fn.handleActionOnDemand();

          YahVideoPlayer.previewLength = res.data.previewLength;
          YahVideoPlayer.isVideoFree = res.data.isVideoFree;
          YahVideoPlayer.videoEnable = res.data.videoEnable;
          YahVideoPlayer.activityNonce = res.data.activityNonce;
          YahVideoPlayer.journeySeriesDayData = legId + '_' + res.data.videoId;
          YahVideoPlayer.initializePlayer(res.data.videoId, res.data.videoCode, res.data.isVideoFree);

          $('html, body').animate({scrollTop: $featuredVideo.offset().top}, 1000, 'swing');
        }
      }).fail(function (request, status, error) {
      });
    },
    updateDayWatched: function (jsId, jsDayData, formCheck) {
      $.fn.callAjaxGlobal({
        url: yahAjax.ajaxurl,
        method: 'POST',
        data: {
          action: 'yah_js_update_log',
          jsId: jsId,
          jsDayData: jsDayData,
          formCheck: formCheck,
        },
        loader: true
      }).done(function (res) {
        if (res.success) {
          $('[data-js-day-watched="' + jsDayData + '"]').toggleClass('watched');
        }
      }).fail(function (request, status, error) {
      });
    },
    destroy: function () {
      $.removeData(this.element[0], pluginName);
    }
  };

  $.fn[pluginName] = function (options, params) {
    return this.each(function () {
      var instance = $.data(this, pluginName);

      if (!instance) {
        $.data(this, pluginName, new Plugin(this, options));
      } else if (instance[options]) {
        instance[options](params);
      }
    });
  };

  $.fn[pluginName].defaults = {};

  $(function () {
    $('[data-' + pluginName + ']')[pluginName]();
  });

}(jQuery, window));
