; (function ($, window, undefined) {
  'use strict';

  var pluginName = 'toggle-action',
    click = 'click.' + pluginName,
    documentClick = 'document-click.' + pluginName,
    classActive = 'active';

  function Plugin(element, options) {
    this.element = $(element);
    this.options = $.extend({}, $.fn[pluginName].defaults, this.element.data(), options);
    this.init();
  }

  Plugin.prototype = {
    init: function () {
      this.$doc = $(document);
      this.cacheDOM();
      this.handleEvent();
    },

    cacheDOM: function () {
      this.$currentBtn = null;
      this.$currentList = null;
      this.dataSend = {};
    },

    handleEvent: function () {
      var that = this;

      this.element
        .off(click)
        .on(click, function (e) {
          e.preventDefault();
          e.stopPropagation();
          that.handleActionList($(this));
        });

      $('[data-action-item]')
        .off(click)
        .on(click, function (e) {
          e.preventDefault();

          var $target = $(e.currentTarget),
            idList = $target.closest('.action-list').attr('id'),
            $btnActive = $('[data-target-action="#'+ idList +'"]'),
            dataItem = $target.data(),
            hasPlaylistActionControl = $('#' + idList).is('[data-playlist-control]'),
            playlistModal = dataItem.toggleModal;

          that.getCurrentAction($btnActive);

          if (hasPlaylistActionControl) {
            e.stopPropagation();

            that.hideAction();
            $(playlistModal).modal('show');
            return;
          }

          that.dataSend = dataItem;

          if (playlistModal && playlistModal.length) {
            that.hideAction();
            $(playlistModal)['playlist-framework-modal']('showModal', {
              modalId: playlistModal,
              dataItem: that.dataSend
            });
          } else {
            that.callAjax(function(res) {
              that.handleActionCall(res, $target);
            });
          }
        });

      this.element
        .off('trigger-hide-action')
        .on('trigger-hide-action', function (e) {
          that.getCurrentAction($(e.currentTarget));
          that.hideAction();
        });

        this.$doc
        .off(documentClick)
        .on(documentClick, function(e, node) {
          var $actionActive = $('[data-target-action].active');

          if ($actionActive.length) {
            that.getCurrentAction($actionActive);
            that.clickOuside(node);
          }
        });
    },

    clickOuside: function(node) {
      var target = node.target,
        checkTarget = function ($main, $sub) {
          var condition = !$main.is(target) &&
            $main.has(target).length === 0 &&
            $main.hasClass(classActive) &&
            !$sub.is(target) &&
            $sub.has(target).length === 0 &&
            $sub.hasClass(classActive);

            return condition;
          };

      if (checkTarget(this.$currentBtn, this.$currentList)) {
        this.hideAction();
      }
    },

    handleActionCall: function(res, $target) {
      if (res.success && res.data.text) {
        $target.data('is-save', res.data.saved ? 0 : 1);
        $target.text(res.data.text);
      }

      this.hideAction();
    },

    handleActionList: function ($target) {
      var isActive   = $target.hasClass(classActive),
          $btnAction = $('[data-toggle-action]');

      this.getCurrentAction($target);

      $btnAction.not($target).each(function () {
        var $actionList = $(this)
                          .parents('.demand-item-bubble-list')
                          .siblings('.action-list');

        $(this).removeClass(classActive);
        $actionList.removeClass(classActive);
      });

      if (isActive) {
        this.hideAction();
      } else {
        this.showAction();
      }
    },

    getCurrentAction: function ($target) {
      var idList = $target.data('target-action');

      this.$currentBtn = $target;
      this.$currentList = $(idList);
    },


    hideAction: function () {
      this.$currentBtn.removeClass(classActive);
      this.$currentList.removeClass(classActive);
    },

    showAction: function () {
      this.$currentBtn.addClass(classActive);
      this.$currentList.addClass(classActive);
    },

    callAjax: function (cb) {
      var that = this,
        data = this.dataSend;

      $.ajax({
        type: that.options.method,
        url: that.options.url,
        data: data,
        cache: false,
        success: function (res) {
          if (typeof cb === 'function') {
            cb(res);
          }
        },
        error: function (err) {
          console.log(err);
        }
      });
    },

    destroy: function () {
      $.removeData(this.element[0], pluginName);
    }
  };

  $.fn[pluginName] = function (options, params) {
    return this.each(function () {
      var instance = $.data(this, pluginName);
      if (!instance) {
        $.data(this, pluginName, new Plugin(this, options));
      } else if (instance[options]) {
        instance[options](params);
      }
    });
  };

  $.fn[pluginName].defaults = {
    method: 'POST',
    url: yahAjax.ajaxurl
  };

  $(function () {
    $('[data-' + pluginName + ']')[pluginName]();
  });

}(jQuery, window));
