;(function ($, window, undefined) {
  'use strict';

  var pluginName = 'playlist-action-control',
    click = 'click.' + pluginName,
    hideClass = 'd-none',
    inProgress = false;

  function Plugin(element, options) {
    this.element = $(element);
    this.options = $.extend({}, $.fn[pluginName].defaults, this.element.data(), options);
    this.init();
  }

  Plugin.prototype = {
    init: function () {
      var $that = this;

      $that.$actionBtnDelete = $that.element.find('[data-delete-playlist]');
      $that.$modalDeletePlaylist = $('[data-playlist-delete-modal]');
      $that.$modalBtnDelete = $that.$modalDeletePlaylist.find('[data-btn-accept]');
      $that.$modalDeleteMessage = $that.$modalDeletePlaylist.find('.general-error');
      $that.$formDelete = $that.$modalDeletePlaylist.find('form.form');
      $that.$loadingDelete = $that.$modalDeletePlaylist.find('.img-loading');

      $that.$actionBtnRename = $that.element.find('[data-rename-playlist]');
      $that.$modalRenamePlaylist = $('[data-playlist-rename-modal]');
      $that.$modalBtnRename = $that.$modalRenamePlaylist.find('[data-btn-rename-playlist]');
      $that.$modalNewPlaylistNameInput = $that.$modalRenamePlaylist.find('[name="new-playlist-name"]');
      $that.$modalRenameMessage = $that.$modalRenamePlaylist.find('.general-error');
      $that.$formRename = $that.$modalRenamePlaylist.find('form.form');
      $that.$loadingRename = $that.$modalRenamePlaylist.find('.img-loading');

      $that.$actionPlaylistRemoveVideo = $that.element.find('[data-playlist-remove-video]');

      $that.$actionBtnDeleteCached = null;
      $that.$actionBtnRenameCached = null;

      $that.handleEvents();
    },
    handleEvents: function () {
      var $that = this;

      $that.$actionBtnRename.off(click).on(click, function () {
        var $item = $(this),
          playlistName = $item.data('playlist-name');

        $that.$modalRenameMessage.html('');
        $that.$actionBtnRenameCached = $item;
        $that.$modalRenamePlaylist.find('input[name="new-playlist-name"]').val(playlistName).focus();
        if (typeof playlistName !== 'undefined' && playlistName.length > 0) {
          $that.$modalBtnRename.prop('disabled', false);
        }
      });

      $that.$modalBtnRename.off(click).on(click, function (e) {
        e.preventDefault();

        $that.renamePlaylistNameEvent($(this));
      });

      $that.$modalNewPlaylistNameInput.off('input').on('input', function (e) {
        e.preventDefault();

        $that.$modalRenameMessage.html('');

        if ($that.$modalNewPlaylistNameInput.val().length > 0) {
          $that.$modalBtnRename.prop('disabled', false);
        } else {
          $that.$modalBtnRename.prop('disabled', true);
        }
      });

      $that.$actionBtnDelete.off(click).on(click, function (e) {
        e.preventDefault();

        $that.$modalDeleteMessage.html('').removeClass('mb-3');
        $that.$modalBtnDelete.prop('disabled', false);
        $that.$actionBtnDeleteCached = $(this);
      });

      $that.$modalBtnDelete.off(click).on(click, function (e) {
        e.preventDefault();

        $that.$modalDeleteMessage.html('').removeClass('mb-3');
        $that.deletePlaylistEvent();
      });

      $that.$actionPlaylistRemoveVideo.off(click).on(click, function () {
        $that.removeVideoFromPlaylist($(this));
      });

      // Update height header on Playlist page
      $('.playlist-wrapper .nav-link')
        .on('shown.bs.tab', function () {
          $that.updateHeightHeader();
        });

      $('[data-playlist-delete-modal], [data-playlist-rename-modal]')
        .on('show.bs.modal hidden.bs.modal', function () {
          $that.updateHeightHeader();
        });

      $('.lists-accordion-collapse')
        .on('shown.bs.collapse hidden.bs.collapse', function () {
          $that.updateHeightHeader();
        });
    },
    renamePlaylistNameEvent: function ($btnRenamePlaylist) {
      var $that = this,
        newPlaylistName = $that.$modalNewPlaylistNameInput.val(),
        $playlistItem = $that.$actionBtnRenameCached.parents('.lists-accordion-item').find('.lists-accordion-desc h5');

      if (typeof newPlaylistName !== 'undefined' && newPlaylistName) {
        $that.$modalBtnRename.prop('disabled', true);

        if (!inProgress) {
          inProgress = true;
          $that.$formRename.addClass(hideClass);
          $that.$loadingRename.removeClass(hideClass);

          $.fn.callAjaxGlobal({
            url: yahAjax.ajaxurl,
            method: 'POST',
            data: {
              action: $btnRenamePlaylist.data('action'),
              playlistId: $that.$actionBtnRenameCached.data('playlist-id'),
              playlistName: newPlaylistName,
              formCheck: $btnRenamePlaylist.data('form-check')
            },
            loader: true
          }).done(function (res) {
            if (res && res.success) {
              $that.$modalRenamePlaylist.modal('hide');
              $playlistItem.text(res.data.playlistName);
              $that.$actionBtnRenameCached.data('playlist-name', res.data.playlistName);
            } else {
              $that.$modalRenameMessage.html(res.data.message);
              $that.$modalNewPlaylistNameInput.focus();
            }

            $that.$modalBtnRename.prop('disabled', false);
            inProgress = false;
            $that.$formRename.removeClass(hideClass);
            $that.$loadingRename.addClass(hideClass);
          }).fail(function (request, status, error) {
            $that.$modalRenameMessage.html('Invalid request.');
            $that.$modalNewPlaylistNameInput.focus();
            $that.$modalBtnRename.prop('disabled', false);
            inProgress = false;
            $that.$formRename.removeClass(hideClass);
            $that.$loadingRename.addClass(hideClass);
          });
        }
      } else {
        $that.$modalRenameMessage.html('The playlist name is required.');
        $that.$modalNewPlaylistNameInput.focus();
      }
    },
    deletePlaylistEvent: function () {
      var $that = this,
        $btnDelete = $that.$actionBtnDeleteCached;

      $that.$modalBtnDelete.prop('disabled', true);
      if (!inProgress) {
        inProgress = true;
        $that.$formDelete.addClass(hideClass);
        $that.$loadingDelete.removeClass(hideClass);

        $.fn.callAjaxGlobal({
          url: yahAjax.ajaxurl,
          method: 'POST',
          data: {
            action: $btnDelete.data('action'),
            playlistId: $btnDelete.data('playlist-id'),
            formCheck: $btnDelete.data('form-check')
          },
          loader: true
        }).done(function (res) {
          if (res && res.success) {
            $that.$modalDeletePlaylist.modal('hide');
            $btnDelete.parents('.lists-accordion-item').remove();
          } else {
            $that.$modalDeleteMessage.html(res.data.message).addClass('mb-3');
          }

          $that.$modalBtnDelete.prop('disabled', false);
          inProgress = false;
          $that.$formDelete.removeClass(hideClass);
          $that.$loadingDelete.addClass(hideClass);
        }).fail(function (request, status, error) {
          $that.$modalDeleteMessage.html('Invalid request.').addClass('mb-3');
          $that.$modalBtnDelete.prop('disabled', false);
          inProgress = false;
          $that.$formDelete.removeClass(hideClass);
          $that.$loadingDelete.addClass(hideClass);
        });
      }
    },
    removeVideoFromPlaylist: function ($btn) {
      var data = $btn.data(),
        $videoItem = $btn.parents('.watched-item'),
        $playlistTotalVideos = $('#playlist-total-videos-' + data.playlistId),
        totalVideos = parseInt($playlistTotalVideos.text());

      $.fn.callAjaxGlobal({
        url: yahAjax.ajaxurl,
        method: 'POST',
        data: {
          action: data.action,
          videoId: data.videoId,
          playlistId: data.playlistId,
          formCheck: data.formCheck
        },
        loader: true
      }).done(function (res) {
        $videoItem.remove();
        totalVideos--;
        $playlistTotalVideos.text(totalVideos > 0 ? totalVideos : 0);
      }).fail(function (request, status, error) {
      });
    },
    updateHeightHeader: function () {
      var $header = $('header'),
        $headerMain = $('.header-main');

      setTimeout(function () {
        $headerMain.trigger('update-height', $header);
      }, 0);
    },
    destroy: function () {
      $.removeData(this.element[0], pluginName);
    }
  };

  $.fn[pluginName] = function (options, params) {
    return this.each(function () {
      var instance = $.data(this, pluginName);
      if (!instance) {
        $.data(this, pluginName, new Plugin(this, options));
      } else if (instance[options]) {
        instance[options](params);
      }
    });
  };

  $.fn[pluginName].defaults = {};

  $(function () {
    $('[data-' + pluginName + ']')[pluginName]();
  });

}(jQuery, window));
