; (function ($, window, undefined) {
  'use strict';

  var pluginName = 'onboarding-tabs',
    classActive = 'active',
    classDisabled = 'disabled';

  function Plugin(element, options) {
    this.element = $(element);
    this.options = $.extend({}, $.fn[pluginName].defaults, this.element.data(), options);
    this.init();
  }

  Plugin.prototype = {
    init: function () {
      this.$formProcess = $('#form-onboarding-process');
      this.$processContainer = $('[data-onboarding-process]');
      this.$contentTabs = this.$processContainer.find('[data-content]');
      this.$btnSkipStep = this.$processContainer.find('[data-skip-step]');
      this.$btnNextStep = this.$processContainer.find('[data-next-step]');
      this.$btnBackStep = this.$processContainer.find('[data-back-step]');
      this.$headerDesktop = this.element.find('.header-main-desktop');
      this.$headerMobile = this.element.find('.header-main-mobile');
      this.$headerCurrent = $.fn.isMobile() ? this.$headerMobile : this.$headerDesktop;
      this.$navTabs = this.element.find('[data-item]');
      this.$navTabStart = this.element.find('[data-onboarding-start]');
      this.handleEvent();
      this.handleShowBack();
    },

    handleEvent: function () {
      var that = this;

      this.$navTabStart.on('click', function (e) {
        e.preventDefault();

        that.$navTabs.eq(0).click();
      });

      this.$navTabs.on('click', function (e) {
        var $currentItem = $(e.currentTarget),
          idTab = $currentItem.data('item');

        if ($currentItem.hasClass(classActive)) {
          return;
        }

        that.$navTabs.removeClass(classActive);
        that.$contentTabs.addClass('d-none');
        that.$processContainer.find('[data-content="' + idTab + '"]').removeClass('d-none');
        that.element.find('[data-item="' + idTab + '"]').addClass(classActive).removeClass(classDisabled);

        if ($.fn.isMobile()) {
          if ($('.onboarding-thumbnail .row').hasClass('slick-initialized')) {
            $('.onboarding-thumbnail .row').slick('unslick');
          }

          $.fn.initSliderOnBoardingProcess($('[data-content="' + idTab + '"]'));
        }

        that.handleShowBack();
      });

      this.$btnNextStep.on('click', function (e) {
        var $stepCurrent = $(this).closest('[data-content]'),
          $valueSelected = $stepCurrent.find('input[type="hidden"]').val();

        if ($valueSelected.length) {
          that.handleNextStep(true);
        }
      });

      this.$btnSkipStep.on('click', function (e) {
        e.preventDefault();
        that.handleNextStep(false);
      });

      this.$btnBackStep.on('click', function (e) {
        e.preventDefault();

        var $itemBack = that.$headerCurrent.find('[data-item].active').prev();

        if ($itemBack.length > 0) {
          $itemBack.trigger('click');
        }
      });
    },

    handleNextStep: function (isValid) {
      var that = this,
          $itemNext = that.$headerCurrent.find('[data-item].active').next(),
          $itemActive = that.element.find('[data-item].active');

      if ($itemNext.length > 0) {
        if (isValid) {
          $itemActive.addClass('completed');
        }

        $itemNext.trigger('click');
      } else {
        this.submitForm();
      }
    },

    handleShowBack: function () {
      var $elmentPrev = this.$headerCurrent.find('[data-item].active').prev();

      if (typeof $elmentPrev.data('item') !== 'undefined') {
        this.$btnBackStep.find('span').show();
      } else {
        this.$btnBackStep.find('span').hide();
      }
    },

    submitForm: function () {
      var that = this,
          dataForm = this.generateDataSend();

      ywah_onboarding_helper.submit_onboarding_process_step(that.$formProcess, dataForm, function () {
        window.location = that.$formProcess.data('onboarding-redirect');
      });
    },

    generateDataSend: function () {
      var dataSend = {},
          $allInputHidden = this.$formProcess.find('input[type="hidden"]');

      $allInputHidden.each(function () {
        var $el = $(this),
          valueEl = $el.val(),
          nameEl = $el.attr('name');

        dataSend[nameEl] = valueEl;
      });

      return dataSend;
    },

    destroy: function () {
      $.removeData(this.element[0], pluginName);
    }
  };

  $.fn[pluginName] = function (options, params) {
    return this.each(function () {
      var instance = $.data(this, pluginName);
      if (!instance) {
        $.data(this, pluginName, new Plugin(this, options));
      } else if (instance[options]) {
        instance[options](params);
      }
    });
  };

  $.fn[pluginName].defaults = {};

  $(function () {
    $('[data-' + pluginName + ']')[pluginName]();
  });

}(jQuery, window));
