/* Options:
  - maxHeight: number // is maximum number of .panel-item height which the plugin take effect
  - extraStyles: class name of custom CSS which you want to add for panel box
*/
;(function($, window, undefined) {
  'use strict';

  var pluginName = 'textpanel',
    body = $('body'),
    win = $(window);

  var toggleBox = function(el, that, status) {
    if(status) {
      el.removeClass('open');
    } else {
      that.setPotstion();

      el.addClass('open')
        .css('height', 'auto');
    }
  };

  function Plugin(element, options) {
    this.element = $(element);
    this.options = $.extend({}, $.fn[pluginName].defaults, this.element.data(), options);
    this.init();
  }

  Plugin.prototype = {
    init: function() {
      var resizeTimer,
        that = this,
        extraClass = this.options.extraStyles ? this.options.extraStyles : '',
        moreLink = '<button type="button" class="view-more"><span class="inner"><span></span><span></span><span></span></span></button>';

      this.element.wrap('<div class="textpanel-wrap based-styles ' + extraClass + '" style="height: ' + this.options.maxHeight + 'px"></div>');

      if(this.element.height() > this.options.maxHeight) {
        var el = this.element.closest('.textpanel-wrap');

        el.append(moreLink);

        this.clonedEl = el
          .clone()
          .addClass('cloned')
          .appendTo(body);

        this.element
          .closest('.textpanel-wrap')
          .on('click.' + pluginName, '.view-more', function() {
            var current  = $('.textpanel-wrap.open');

            if(current.length && !current.is(that.clonedEl)) {
              toggleBox(current, that, true);
            }

            toggleBox(that.clonedEl, that, false);
        });

        this.clonedEl.on('click.' + pluginName, '.view-more', function() {
          toggleBox($(this).closest('.textpanel-wrap'), that, true);
        });
      }

      win.on('resize.' + pluginName, function() {
        clearTimeout(resizeTimer);
        resizeTimer = setTimeout(function() {
            that.hide();
        }, 100);
      });
    },
    hide: function() {
      toggleBox($('.textpanel-wrap.open'), this, true);
    },
    setPotstion: function() {
      if(!!this.clonedEl) {
        var el = this.element.closest('.textpanel-wrap'),
            currentWidth = el.width(),
            offset = el.offset(),
            offsetTop = offset.top,
            offsetLeft = offset.left;

        this.clonedEl
          .css({
            'top': offsetTop,
            'left': offsetLeft,
            'width': currentWidth + 30 // plus gutter width
          });
      }
    },
    clean: function(){
        if(!!this.clonedEl) {
            this.clonedEl.remove();
        }

        this.element
            .unwrap()
            .parent()
            .find('.view-more')
            .remove();
    },
    destroy: function() {
      this.clean();

      $.removeData(this.element[0], pluginName);
    },
    reInit: function(){
      this.clean();
      this.init();
    }
  };

  $.fn[pluginName] = function(options, params) {
    return this.each(function() {
      var instance = $.data(this, pluginName);
      if (!instance) {
        $.data(this, pluginName, new Plugin(this, options));
      } else if (instance[options]) {
        instance[options](params);
      }
    });
  };

  $.fn[pluginName].defaults = {
    maxHeight: 80,
    extraStyles: null
  };

  $(function() {
    $('[data-' + pluginName + ']')[pluginName]();
  });

}(jQuery, window));
