;(function ($, window, undefined) {
  'use strict';

  var pluginName = 'playlist-watched',
    waypoints
  ;

  function Plugin(element, options) {
    this.element = $(element);
    this.options = $.extend({}, $.fn[pluginName].defaults, this.element.data(), options);
    this.init();
  }

  Plugin.prototype = {
    init: function () {
      this.$win = $(window);
      this.$watchedWrapper = this.element.find('[data-playlist-watched-wrapper]');
      this.itemsPerPage = this.options.itemsPerPage ? parseInt(this.options.itemsPerPage) : 12;

      this.getVideos(1);
      this.handleEvent();
    },

    handleEvent: function () {
      var that = this;

      this.$win.on('scrolled', function () {
        if (waypoints && that.element.is(':visible')) {
          waypoints.enable();
        }
      });
    },

    getVideos: function (pageNumber) {
      var that = this;

      $.fn.callAjaxGlobal({
        url: yahAjax.ajaxurl,
        method: "GET",
        data: {
          action: that.options.action,
          ipp: that.itemsPerPage,
          page: pageNumber,
          formcheck: that.options.nonce,
        },
        loader: true
      }).done(function (res) {
        if (res && res.success) {
          if (waypoints) {
            waypoints.destroy();
          }

          if (parseInt(res.data.total) === 0) {
            that.renderErrorBlock(that.options.errorMessage);
          } else {
            that.renderVideoItems(res.data.watchedHtml, res.data.total, pageNumber);
          }
        }
      }).fail(function (request, status, error) {
        that.renderErrorBlock(request.responseText);
      });
    },

    convertWatchedDate: function () {
      var that      = this,
          $itemDate = this.element.find('[data-convert-to-date]').not('.convert-date');

      $itemDate.each(function () {
        var
          timeUnix  = $(this).data('convert-to-date'),
          toDate    = moment(timeUnix, 'X').format('lll'),
          timeStamp = moment(new Date(toDate)).format(that.options.dateFormat)
        ;

        $(this).html(timeStamp).addClass('convert-date');
      });
    },

    renderVideoItems: function (htmlItems, total, page) {
      var
        that = this,
        renderedItems = page * that.itemsPerPage
      ;

      if (renderedItems > total) {
        renderedItems = total;
      }
      if (page === 1) {
        that.$watchedWrapper.html(htmlItems);
      } else {
        that.$watchedWrapper.append(htmlItems);
      }

      this.convertWatchedDate();

      $(window).trigger('scrollmagic:change');

      if (renderedItems < total) {
        waypoints = new Waypoint({
          element: that.$watchedWrapper,
          handler: function (direction) {
            if (direction === 'down') {
              that.getVideos(page + 1);
            }
          },
          enabled: false,
          offset: 'bottom-in-view'
        });
      }
    },

    renderErrorBlock: function (msg) {
      this.$watchedWrapper.html('<label class="text-danger watched-message">' + msg + '</label>');
    },

    destroy: function () {
      $.removeData(this.element[0], pluginName);
    }
  };

  $.fn[pluginName] = function (options, params) {
    return this.each(function () {
      var instance = $.data(this, pluginName);
      if (!instance) {
        $.data(this, pluginName, new Plugin(this, options));
      } else if (instance[options]) {
        instance[options](params);
      }
    });
  };

  $.fn[pluginName].defaults = {
    dateFormat: 'MM/DD/YYYY'
  };

  $(function () {
    $('[data-' + pluginName + ']')[pluginName]();
  });

}(jQuery, window));
