;(function ($, window, undefined) {
  'use strict';

  var
    pluginName = 'filter-demand',
    click = 'click.' + pluginName,
    waypoints
  ;

  function Plugin(element, options) {
    this.element = $(element);
    this.options = $.extend({}, $.fn[pluginName].defaults, this.element.data(), options);
    this.init();
  }

  Plugin.prototype = {
    init: function () {
      this.$dropdownItem = this.element.find('.dropdown-item:not(.empty-line)');
      this.$demandVideosList = $('[data-demand-videos-list]');
      this.$filterListItems = $('.filter-demand-selections');
      this.$boxFilter = $('[data-box-filter]');
      this.hashPrefixLength = this.options.hashPrefix.length;
      this.allVideosHash = this.options.hash.substring(this.hashPrefixLength);
      this.itemsPerPage = this.options.itemsPerPage ? parseInt(this.options.itemsPerPage) : 12;

      this.$boxFilter.addClass('box-filter');

      this.renderFilterList();
      this.handleFilterEvents();
    },

    renderFilterList: function () {
      var
        $that = this,
        hash = (window.location.hash.length <= 0) ? $that.options.hash : window.location.hash,
        isAllVideos = $that.options.hash === hash,
        filters = hash.substring($that.hashPrefixLength).split('/'),
        count = 0
      ;

      // #/all-videos
      if (isAllVideos) {
        $that.renderAllVideosFilter();
        return;
      }

      // Render filter list
      $.each(filters, function (idx, filterSlug) {
        var
          $filterItem = $('.dropdown-item[data-filter-slug="' + filterSlug + '"]'),
          filterGroup = $filterItem.parents('[data-filter-group-items]')
        ;

        if ($filterItem.length) {
          $filterItem.addClass('active');
          $that.renderFilterListItem($filterItem.text(), filterSlug, filterGroup.attr('data-group-name'));
          count++;
        }
      });

      // Empty filter, render default
      if (count < 1) {
        $that.updateLocationHash($that.options.hash);
        $that.renderFilterList();
      }

      // Get first page of result
      $that.getVideos(1);
    },
    renderFilterListItem: function (filterText, filterSlug, filterGroup) {
      var $itemInList = this.$filterListItems.find('[data-filter-slug="' + filterSlug + '"]');

      if (!$itemInList.length) {
        return this.$filterListItems.append(
          '<span class="filter-demand-label" data-filter-group="' + filterGroup + '"' +
          '      data-filter-slug="' + filterSlug + '">' +
          '  <span class="filter-demand-slug">' + filterText + '</span>' +
          '  <span class="filter-demand-close" data-remove-filter-item>' +
          '    <span class="icon-bar"></span>' +
          '    <span class="icon-bar"></span>' +
          '  </span>' +
          '</span>'
        );
      }

      $(window).trigger('scrollmagic:change');
    },
    handleFilterEvents: function () {
      var
        $that = this,
        $btnRemoveFilterItem = '[data-remove-filter-item]',
        $btnResetFilter = '[data-reset-filter]',
        _jScrollPane,
        _jScrollPaneAPI
      ;

      $('[data-filter-group-items]')
        .on('show.bs.dropdown', function () {
          $that.$boxFilter.css({'overflow': 'hidden'});
        })
        .on('shown.bs.dropdown', function () {
          var $el = $(this),
              $listItem = $el.find('.dropdown-menu');

          if (undefined !== _jScrollPane) {
            _jScrollPaneAPI.destroy();
          }

          $listItem.find('[class^="dropdown"]')
            .wrapAll($('<div class="scroll-panel"></div>')
              .css({
                'max-height': $listItem.css('max-height'),
                'width': $listItem.width()
              })
            );

          _jScrollPane    = $listItem.find('.scroll-panel').jScrollPane();
          _jScrollPaneAPI = _jScrollPane.data('jsp');
        })
        .on('hidden.bs.dropdown', function () {
          $that.$boxFilter.removeAttr('style');

          var $el = $(this),
              $listItem = $el.find('.dropdown-menu'),
              $item = $listItem.find('.dropdown-item');

          _jScrollPaneAPI.destroy();
          _jScrollPane = undefined;
          $item.unwrap();
        });

      // prevent close dropdown when clicking on scroll bar (jscrollpane)
      $('.dropdown-menu').on("click.bs.dropdown", function (e) {
        e.stopPropagation();
      });

      // Handle filter dropdown
      $that.$dropdownItem.off(click).on(click, function () {
        var
          $item = $(this),
          slugName = $item.data('filter-slug'),
          $group = $item.closest('[data-filter-group-items]'),
          groupName = $group.data('group-name'),
          $selectionItem = $that.$filterListItems.find('[data-filter-group="' + groupName + '"]')
        ;

        // close dropdown when selecting item
        $group.find('.dropdown-toggle').dropdown('toggle');

        if ($selectionItem.length) {
          var
            selectionSlug = $selectionItem.attr('data-filter-slug'),
            hash = window.location.hash.replace(selectionSlug, slugName)
          ;

          $group.find('.dropdown-item').removeClass('active');
          $selectionItem.attr('data-filter-slug', slugName);
          $selectionItem.find('.filter-demand-slug').text($item.text());

          $that.updateLocationHash(hash);
          $that.renderFilterList();
        } else {
          $that.updateHash(slugName, true);
        }
      });

      // Handle remove filter
      $that.$filterListItems.off(click).on(click, $btnRemoveFilterItem, function () {
        var
          $item = $(this).parent('.filter-demand-label'),
          filterGroup = $item.data('filter-group'),
          filterSlug = $item.data('filter-slug')
        ;

        $item.remove();
        $('[data-group-name="' + filterGroup + '"]').find('.dropdown-item').removeClass('active');
        $that.updateHash(filterSlug, false);
      });

      // Handle reset filter
      $($btnResetFilter).off(click).on(click, function () {
        $('[data-filter-group-items] .dropdown-item').removeClass('active');
        $that.updateHash($that.allVideosHash, false);
      });
    },
    renderAllVideosFilter: function () {
      this.$filterListItems.find('.filter-demand-label:not([data-reset-filter])').remove();
      this.updateLocationHash(this.options.hash);
      this.renderFilterListItem('All Videos', this.allVideosHash, '');
      this.getVideos(1);
    },
    updateLocationHash: function (hash) {
      window.location.hash = hash;
    },
    updateHash: function (itemHash, isAdd) {
      var
        $that = this,
        currentHash = window.location.hash,
        hash = currentHash.substring($that.hashPrefixLength).split('/'),
        idx = $.inArray(itemHash, hash)
      ;

      if (itemHash === $that.allVideosHash) {
        $that.updateLocationHash($that.options.hash);
        $that.renderFilterList();
        return;
      }

      // remove all
      if (hash.length > 0) {
        var allVideosIdx = $.inArray($that.allVideosHash, hash);

        if (allVideosIdx !== -1) {
          hash.splice(allVideosIdx, 1);

          // Remove in filter list
          $that.$filterListItems.find('.filter-demand-label[data-filter-slug="' + $that.allVideosHash + '"]').remove();

          // Update hash on URL
          window.location.hash = $that.options.hashPrefix + hash.join('/');
        }
      }
      // Add item to hash
      if (isAdd && idx === -1) {
        hash.push(itemHash);
      }

      // Delete item from hash
      if (!isAdd && idx !== -1) {
        hash.splice(idx, 1);
      }

      // Render filter if hash change
      hash = $that.options.hashPrefix + hash.join('/');
      if (currentHash !== $that.options.hashPrefix + hash) {
        $that.updateLocationHash(hash);
        $that.renderFilterList();
      }
    },
    renderErrorBlock: function (msg) {
      this.element.find('.filter-result').remove();
      this.$demandVideosList.html('<label class="text-danger filter-message">' + msg + '</label>');
    },
    getVideos: function (pageNumber) {
      var
        $that = this,
        $selections = $('.filter-demand-selections'),
        hasStaffFilter = $selections.find('[data-filter-group="teacher"]').length
      ;

      $.fn.callAjaxGlobal({
        url: yahAjax.ajaxurl,
        data: {
          action: $that.options.action,
          hash: window.location.hash,
          staff: hasStaffFilter ? 1 : 0,
          itemsPerPage: $that.itemsPerPage,
          page: pageNumber
        },
        loader: true
      }).done(function (res) {
        if (res && res.success) {
          if (waypoints) {
            waypoints.destroy();
          }

          if (parseInt(res.data.total) === 0) {
            $that.renderErrorBlock($that.options.errorMessage);
          } else {
            $that.renderVideoItems(res.data.asset_list_html, res.data.total, pageNumber);
          }
        }
      }).fail(function (request, status, error) {
        $that.renderErrorBlock(request.responseText);
      });
    },
    renderVideoItems: function (htmlItems, total, page) {
      var
        $that = this,
        renderedItems = page * $that.itemsPerPage
      ;

      if (renderedItems > total) {
        renderedItems = total;
      }

      $that.element.find('.filter-result').remove();
      $that.$filterListItems.before('<label class="filter-result">Showing <span>' + renderedItems + '</span> of ' + total + ' videos</label>');
      if (page === 1) {
        $that.$demandVideosList.html(htmlItems);
      } else {
        $that.$demandVideosList.append(htmlItems);
      }

      // Init other plugins
      $(window).trigger('scrollmagic:change');
      $('[data-toggle-action]')['toggle-action']('init');
      $.fn.handleActionOnDemand();

      if (renderedItems < total) {
        waypoints = new Waypoint({
          element: $that.$demandVideosList,
          handler: function (direction) {
            if (direction === 'down') {
              $that.getVideos(page + 1);
            }
          },
          offset: "bottom-in-view"
        });
      }
    },

    destroy: function () {
      $.removeData(this.element[0], pluginName);
    }
  };

  $.fn[pluginName] = function (options, params) {
    return this.each(function () {
      var instance = $.data(this, pluginName);

      if (!instance) {
        $.data(this, pluginName, new Plugin(this, options));
      } else if (instance[options]) {
        instance[options](params);
      }
    });
  };

  $.fn[pluginName].defaults = {
    hashPrefix: '#/',
    hash: '#/all-videos',
  };

  $(function () {
    $('[data-' + pluginName + ']')[pluginName]();
  });

}(jQuery, window));
