;(function ($, window, undefined) {
  'use strict';

  var pluginName = 'onboarding-subscribe',
    classActive = 'active',
    classHidden = 'd-none',
    click = 'click.' + pluginName,
    change = 'change.' + pluginName,
    selectedPlan = null;

  function Plugin(element, options) {
    this.element = $(element);
    this.options = $.extend({}, $.fn[pluginName].defaults, this.element.data(), options);
    this.init();
  }

  Plugin.prototype = {
    init: function () {
      this.$subscriptionTab = this.element.find('[data-subscribe-tab]');
      this.$subscriptionContent = this.element.find('[data-subscribe-content]');
      this.$subscriptionSwitch = this.element.find('[data-subscribe-switch-title]');
      this.$subscriptionPromo = this.element.find('[data-subscribe-promo]');
      this.$subscriptionPromoImg = this.element.find('[data-subscribe-promo-image]');
      this.$subscriptionPlanNextBtn = this.element.find('[data-btn-onboarding-plan-next-btn]');
      this.$subscriptionBackBtn = this.element.find('[data-onboarding-subscribe-back-btn]');
      this.$ccFormInputs = this.element.find('.cc-form-inputs');
      this.$chbxIntlPayment = this.element.find('.intl-payment-wrap');
      this.$intlPayment = this.$chbxIntlPayment.find('[data-intl-payment]');
      this.noCcRequired = false;

      this.handleEvent();
      this.initSwitch();
    },
    handleEvent: function () {
      var that = this;

      this.$subscriptionTab
        .off(click)
        .on(click, function () {
          var $currentActive = $(this),
            idTab = $currentActive.data().subscribeTab,
            planCode = $currentActive.find('input[name="switch_plan"]').val(),
            $checkoutForm = $('.ywah-onboarding-checkout');

          if (typeof planCode === 'undefined' || planCode.length < 1) {
            return;
          }

          selectedPlan = planCode;

          that.element.find('input[name="plan_code"]').val(planCode);
          $('.checkout-form-promo input[name="plan_code"]').val(planCode); // Set plan to Promo Code modal
          $('[data-cart-detail-wrap]').hide();
          that.element.find('input[name="gift_card_code"]').val(''); // Remove gift code
          $('[data-promo-code-open-popup-btn]').removeClass('d-none').addClass('d-inline-block');
          $checkoutForm.find('.title-wrapper').show();
          $checkoutForm.find('.cc-form-inputs').show();
          $checkoutForm.find('.intl-payment-wrap').show();

          if (!$currentActive.hasClass('active')) {
            that.$subscriptionTab.removeClass(classActive);
            $currentActive.addClass(classActive);
            that.$subscriptionContent.removeClass(classActive);
            that.element.find('[data-subscribe-content="' + idTab + '"]').addClass(classActive);
            $('.apply-code span').trigger('click'); // Cancel Promo Code that entered
            if (typeof recurlyData.previewPurchase === 'function') {
              recurlyData.previewPurchase();
            }
          }

          // that.updatePlanTitleBox();
        });

      this.$subscriptionSwitch
        .off(change)
        .on(change, function (e) {
          e.preventDefault();

          that.element.find('[data-subscribe-tab="tab-' + $(this).val() + '"]').trigger('click');
        });

      this.element
        .off('applied-code')
        .on('applied-code', function (e, data) {
          e.preventDefault();
          if (typeof data.noccrequired !== 'undefined' && data.noccrequired) {
            that.element.find('input[name="plan_code"]').val(data.plan_code);

            that.noCcRequired = true;
            that.element.find('.validate-error').hide();
            that.$ccFormInputs.addClass(classHidden);
            that.$chbxIntlPayment.addClass(classHidden);
            that.$subscriptionTab.addClass(classHidden);
            that.$subscriptionContent.addClass(classHidden);
            that.$subscriptionPromo.html(data.plan_data.name ?
              '<h5>' +
              '  <span>SUBSCRIPTION: </span>' +
              '  ' + data.plan_data.name +
              '</h5>' : ''
            ).removeClass(classHidden).css('display', 'block');
            that.$subscriptionPromoImg.removeClass(classHidden).css('display', 'block');
            that.$subscriptionSwitch.parent().css('display', 'none');
          }

          if (typeof recurlyData.previewPurchase === 'function') {
            recurlyData.previewPurchase();
          }
        }).off('cancel-code')
        .on('cancel-code', function (e) {
          e.preventDefault();

          if (that.noCcRequired) {
            that.element.find('input[name="plan_code"]').val(selectedPlan);
            that.noCcRequired = false;
            that.element.find('.validate-error').hide();
            that.$ccFormInputs.removeClass(classHidden);
            that.$ccFormInputs.find('label.error').hide();
            that.$ccFormInputs.find('input[type=text]').val('').removeClass('error');
            that.$ccFormInputs.find('.ico.ico-cards').removeClass().addClass('ico ico-cards');
            that.$chbxIntlPayment.removeClass(classHidden);
            that.$intlPayment.prop('checked', false).triggerHandler('click');
            that.$subscriptionTab.removeClass(classHidden);
            that.$subscriptionContent.removeClass(classHidden);
            that.$subscriptionPromo.addClass(classHidden).css('display', 'none');
            that.$subscriptionPromoImg.addClass(classHidden).css('display', 'none');
            that.$subscriptionSwitch.parent().css('display', 'block');
            that.element.find('.onboarding-plan-item.onboarding-plan-primary').trigger('click');
          }

          if (typeof recurlyData.previewPurchase === 'function') {
            recurlyData.previewPurchase();
          }
        });

      this.$subscriptionBackBtn.off(click).on(click, function (e) {
        e.preventDefault();

        var toStep = $(this).data('to-step');

        if (typeof toStep !== 'undefined' && parseInt(toStep) === 2) {
          $('.onboarding-start-profile').hide();
          $('.onboarding-checkout-plan-list').show();
          $('.onboarding-checkout').hide();
        } else {
          $('.onboarding-start-profile').show();
          $('.onboarding-checkout-plan-list').hide();
          $('.onboarding-checkout').hide();
        }
      });

      this.$subscriptionPlanNextBtn.off(click).on(click, function (e) {
        e.preventDefault();

        var $planForm = $('.ywah-onboarding-plan-list'),
            $selectedPlan = $planForm.find('input[name="switch_plan"]:checked'),
            $selectedPlanWrap = $selectedPlan.parent('label'),
            $checkoutForm = $('.ywah-onboarding-checkout-new'),
            planTitle = $selectedPlanWrap.find('[data-subscribe-title]').html().replace(/<[^>]+>/gi, ' '),
            planDesc = $selectedPlanWrap.find('[data-subscribe-desc]').html();

        $checkoutForm.find('input[name="plan_code"]').val($selectedPlan.val());
        $checkoutForm.find('[data-onboarding-plan-detail-wrap]').html('<h5><span>SUBSCRIPTION:</span>' + planTitle + '</h5>' + planDesc);
        $planForm.closest('.onboarding-checkout-plan-list').hide();
        $('.onboarding-checkout').show();
      });
    },

    updatePlanTitleBox: function () {
      var $tabNotActive = this.element.find('[data-subscribe-tab]:not(.active)'),
        htmlPlanTitle = $tabNotActive.find('[data-subscribe-title]').html().replace(/<[^>]+>/gi, ' ');

      this.$subscriptionSwitch.text('SWITCH TO ' + $('<p>' + htmlPlanTitle + '</p>').text());
    },

    initSwitch: function () {
      this.element.find('[data-subscribe-tab].active').trigger('click');
    },

    destroy: function () {
      $.removeData(this.element[0], pluginName);
    }
  };

  $.fn[pluginName] = function (options, params) {
    return this.each(function () {
      var instance = $.data(this, pluginName);
      if (!instance) {
        $.data(this, pluginName, new Plugin(this, options));
      }
      else if (instance[options]) {
        instance[options](params);
      }
    });
  };

  $.fn[pluginName].defaults = {};

  $(function () {
    $('[data-' + pluginName + ']')[pluginName]();
  });

}(jQuery, window));
