;(function ($, window, undefined) {
  'use strict';

  var
    pluginName = 'upcoming-events',
    upcomingClasses = {},
    upcomingBgImages = {},
    timeoutCountdown = null
  ;

  function Plugin(element, options) {
    this.element = $(element);
    this.options = $.extend({}, $.fn[pluginName].defaults, this.element.data(), options);
    this.allowedAccess = this.options.allowedAccess || false;

    this.init();
  }

  Plugin.prototype = {
    init: function () {
      var $that = this;

      $that.$wrapperSlider = $that.element.find('[data-slider]');
      $that.$bubble = $('[data-bubble-countdown]');

      $that.renderBubble();

      $that.getUpcomingEventData(function () {
        $that.initUpcomingEvents();
      });
    },
    initUpcomingEvents: function () {
      var
        $that = this,
        result = $that.getUpcomingEvents()
      ;

      if (result && result.length) {
        $that.renderUpcomingEvents(result);

        $that.$wrapperSlider.on('init', function (slick) {
          $that.renderCountdown(result[0]);
        });

        $that.$wrapperSlider.on('beforeChange', function (event, slick, currentSlide, nextSlide) {
          $that.$bubble.find('.bubble-wrap').addClass('d-none');
          clearTimeout(timeoutCountdown);
        });

        $that.$wrapperSlider.on('afterChange', function (slick, currentSlide) {
          var eventId = $('.slick-current').find('[data-event-id]').data('event-id'),
            currentItem = $.grep(result, function (e) {
              return e.eventId === eventId;
            });

          if (currentItem.length > 0) {
            $that.$bubble.find('.bubble-wrap').addClass('d-none');
            $that.renderCountdown(currentItem[0]);
          }
        });
      }
    },
    getUpcomingEventData: function (callback) {
      $.fn.callAjaxGlobal({
        url: yahAjax.ajaxurl,
        method: 'POST',
        data: {
          action: this.options.action,
          siteIds: this.options.siteIds,
          formCheck: this.options.formCheck,
          marianatekLocations: this.options.marianatekLocations,
          marianatekShowLiveClasses: this.options.showLiveClasses,
          marianatekShowPhysicalStudios: this.options.showPhysicalStudios,
        },
        loader: true
      }).done(function (res) {
        if (res && res.success) {
          upcomingClasses = res.data.upcomingClasses;
          upcomingBgImages = res.data.bgImages;

          if (typeof callback === 'function') {
            callback();
          }
        }
      }).fail(function (request, status, error) {
      });
    },
    getUpcomingEvents: function () {
      var
        numberOfClass = this.options.numberOfClass ? parseInt(this.options.numberOfClass) : 1,
        useEndDate = !!this.options.useEndDate,
        totalItems = upcomingClasses ? upcomingClasses.length : 0,
        currentTimeStamp = moment().unix(),
        result = [],
        countRes = 0
      ;

      for (var i = 0; i < totalItems; i++) {
        var
          eventData = upcomingClasses[i],
          classTimeStamp = useEndDate ? eventData.endTimeZonedStamp : eventData.startTimeZonedStamp,
          isAvailable = (classTimeStamp > currentTimeStamp) && !eventData.isCanceled;

        if (countRes >= numberOfClass) {
          break;
        }

        if (isAvailable) {
          eventData.eventId = eventData.siteId + '-' + eventData.classId;

          result.push(eventData);
          countRes++;
        }
      }

      if (result.length > 1) {
        result.sort(function (a, b) {
          return a.startTimeZonedStamp > b.startTimeZonedStamp;
        });
      }

      return result;
    },
    renderUpcomingEvents: function (data) {
      var
        $that = this,
        slickHtml = '',
        lineClamp = $that.options.lineClamp || 2
      ;

      data.forEach(function (item) {
        var
          eventId = item.siteId + '-' + item.classId,
          sliderEventId = item.siteId + '_' + item.classId,
          bgImage = upcomingBgImages ? upcomingBgImages[Math.floor(Math.random() * upcomingBgImages.length)] : '',
          level = item.level ? ('<span>' + item.level + '</span>') : ''
        ;

        slickHtml +=
          '<div class="slider-item" style="background-image: url(' + bgImage + ');" data-slider-event-id="' + sliderEventId + '">' +
          '  <img src="' + bgImage + '" alt="">' +
          '  <div class="slider-caption d-md-block slider-description">' +
          '    <h2>' + item.title + '</h2>' +
          '    <h5>' + item.teacherName + level + ' <span>' + item.duration + ' min</span>' +
          '    </h5>' +
          '    <div class="description-paragraph" data-text-overflow-clamp data-line-clamp="' + lineClamp + '">' +
          '      <p>' + item.desc + '</p>' +
          '    </div>' +
          '    <div class="upcoming-cta-result"></div>' +
          '    <div data-event-id="' + eventId + '"></div>' +
          '  </div>' +
          '</div>';
      });

      $that.$wrapperSlider.html(slickHtml);

      // init plugin text-overflow-clamp
      $(window).trigger('hero:ready', [$that.element]);

      setTimeout(function () {
        $that.$wrapperSlider.slick({
          slidesToShow: 1,
          dots: true,
          autoplay: true,
          autoplaySpeed: $that.options.autoplaySpeed,
          pauseOnFocus: true,
          pauseOnHover: true,
          vertical: true,
          arrows: false,
          infinite: true,
          useCSS: false
        }).on('beforeChange', function() {
          $('.slider-item').removeClass('slide-prev slide-next');
        }).on('setPosition', function() {
          $('.slick-current.slick-active').prev().addClass('slide-prev');
          $('.slick-current.slick-active').next().addClass('slide-next');
        });
      }, 500);
    },
    renderErrorMessage: function (data) {
      var msg = data.message;

      $('[data-slider-event-id="' + data.itemId + '"] .upcoming-cta-result').html(msg)
        .toggleClass('d-inline-block', !(typeof msg === 'undefined' || msg === ''));
    },
    renderBubble: function () {
      var
        $bubbleBlock = $('[data-bubble-countdown]'),
        renderNumber = function (num) {
          var html = '<div class="number-grp-wrp">';
          for (var i = num; i >= 0; i--) {
            html += '<div class="num num-' + i + '"><p>' + i + '</p></div>';
          }
          html += '</div>';

          return html;
        };

      $bubbleBlock.html(
        '<div class="bubble-wrap zoom-effect d-none">' +
        '  <div class="fuzzy-bubble solid-color"></div>' +
        '  <div class="description-bubble">' +
        '    <div class="description-bubble__content description-bubble__countdown d-none" data-start-countdown>' +
        '      <p class="text-in-bubble context-3">LIVE IN</p>' +
        '      <div class="timer">' +
        '        <div class="timer--clock">' +
        '          <div class="minutes-group clock-display-grp" data-minutes-group>' +
        '            <div class="first number-grp" data-first-number>' + renderNumber(9) + '</div>' +
        '            <div class="second number-grp" data-second-number>' + renderNumber(9) + '</div>' +
        '          </div>' +
        '          <div class="clock-separator"><p>m</p></div>' +
        '          <div class="seconds-group clock-display-grp" data-seconds-group>' +
        '            <div class="first number-grp" data-first-number>' + renderNumber(9) + '</div>' +
        '            <div class="second number-grp" data-second-number>' + renderNumber(9) + '</div>' +
        '          </div>' +
        '          <div class="clock-separator"><p>s</p></div>' +
        '        </div>' +
        '      </div>' +
        '    </div>' +
        '    <div class="description-bubble__content description-bubble__text d-none" data-end-countdown>' +
        '      <a href="#" class="btn-livestream-join" data-btn-livestream-join>' +
        '         <h5 class="text-in-bubble text-white">' + this.options.ctaJoinText + '</h5>' +
        '      </a>' +
        '    </div>' +
        '  </div>' +
        '</div>'
      );
    },
    renderCountdown: function (item) {
      if (typeof item === 'undefined') {
        return;
      }

      var
        $that = this,
        diffInSeconds = item.startTimeZonedStamp - moment().unix(),
        timeShowBubbleCountdown = this.options.timeShowCountdown ? parseInt(this.options.timeShowCountdown) : 3600, // 60 minutes
        timeShowCtaBtn = this.options.timeShowCta ? parseInt(this.options.timeShowCta) : 300 // 5 minutes
      ;

      if (diffInSeconds < timeShowBubbleCountdown &&
          timeShowBubbleCountdown < this.options.limitTimeShowCountdown) {
        if ($that.allowedAccess) {
          // Have access, check the countdown
          $that.startCountdownEvent(diffInSeconds, timeShowCtaBtn);

          // Update data to join button
          var
            durationMin = parseInt(item.duration), //in minutes
            duration = isNaN(durationMin) ? (-30 * 60) : -60 * durationMin //in seconds
          ;

          $('[data-end-countdown] .btn-livestream-join')
            .attr({
              'data-site-id': item.siteId,
              'data-class-id': item.classId,
              'data-class-schedule-id': item.classSchedId,
              'data-room-name': item.resourceName,
              'data-loc-id': item.locId,
              'data-loc-name': item.location,
              'data-title': item.title,
              'data-teacher': item.teacherName,
              'data-is-free': 0,
              'data-level': item.level,
              'data-yoga-type': item.yogaType,
              'data-start-stamp': item.startTimeZonedStamp,
              'data-end-stamp': parseInt(item.startTimeZonedStamp) - duration,
              'data-plugin-name': pluginName,
            })
            .find('h5').removeClass('text-white')
            .text($that.options.ctaJoinText);
        } else {
          // Show Upgrade button
          $that.$bubble.find('.bubble-wrap').removeClass('d-none');
          $('[data-end-countdown]')
            .removeClass('d-none')
            .find('.btn-livestream-join')
            .removeAttr('data-btn-livestream-join')
            .attr('href', $that.options.ctaUpgradeUrl)
            .find('h5').addClass('text-white')
            .text($that.options.ctaUpgradeText);
        }
      } else {
        $that.$bubble.find('.bubble-wrap').addClass('d-none');
      }
    },
    startCountdownEvent: function (timeCountdown, timeShowCtaBtn) {
      var
        $that = this,
        $el = $that.element,
        minutesGroupEl = $el.find('[data-minutes-group]'),
        secondsGroupEl = $el.find('[data-seconds-group]'),
        minutesFirstNum = minutesGroupEl.find('[data-first-number]'),
        minutesSecondNum = minutesGroupEl.find('[data-second-number]'),
        secFirstNum = secondsGroupEl.find('[data-first-number]'),
        secSecondNum = secondsGroupEl.find('[data-second-number]'),

        animateNum = function (group, arrayValue) {
          TweenMax.killTweensOf(group.find('.number-grp-wrp')[0]);
          TweenMax.to(group.find('.number-grp-wrp')[0], 1, {
            y: -group.find('.num-' + arrayValue)[0].offsetTop
          });
        },
        updateTimerDisplay = function (arr) {
          animateNum(minutesFirstNum, arr[0]);
          animateNum(minutesSecondNum, arr[1]);
          animateNum(secFirstNum, arr[2]);
          animateNum(secSecondNum, arr[3]);
        },
        updateTimer = function () {
          timeShowCtaBtn = timeShowCtaBtn ? timeShowCtaBtn : 300; // 5 minutes
          if (timeCountdown <= 0 || timeCountdown <= timeShowCtaBtn) {
            $that.stopCountdownEvent();
            return;
          }

          $('[data-start-countdown]').removeClass('d-none');

          timeCountdown = timeCountdown - 1;
          var
            numOfMinutes = Math.floor(timeCountdown / 60),
            numOfSeconds = timeCountdown - numOfMinutes * 60,
            minString = numOfMinutes < 10 ? ('0' + numOfMinutes) : numOfMinutes.toString(),
            secString = numOfSeconds < 10 ? ('0' + numOfSeconds) : numOfSeconds.toString(),
            timeString = minString + secString
          ;

          updateTimerDisplay(timeString);
          timeoutCountdown = setTimeout(updateTimer, 1000);
        }
      ;

      setTimeout(function () {
        $that.$bubble.find('.bubble-wrap').removeClass('d-none');
        $('[data-start-countdown]').addClass('d-none');
        $('[data-end-countdown]').addClass('d-none');
      }, 800);

      timeoutCountdown = setTimeout(updateTimer, 1000);
    },
    stopCountdownEvent: function () {
      $('[data-start-countdown]').addClass('d-none');
      $('[data-end-countdown]').removeClass('d-none');
    },
    destroy: function () {
      $.removeData(this.element[0], pluginName);
    }
  };

  $.fn[pluginName] = function (options, params) {
    return this.each(function () {
      var instance = $.data(this, pluginName);

      if (!instance) {
        $.data(this, pluginName, new Plugin(this, options));
      } else if (instance[options]) {
        instance[options](params);
      }
    });
  };

  $.fn[pluginName].defaults = {
    autoplaySpeed: 3000,
    lineClamp: 2,
    allowedAccess: false,
    ctaJoinText: 'Join',
    ctaUpgradeText: 'Upgrade',
    ctaUpgradeUrl: '/account/#payments',
    limitTimeShowCountdown: 6000
  };

  $(function () {
    $('[data-' + pluginName + ']')[pluginName]();
  });

}(jQuery, window));
