(function( $ ){

    $(document).ready(function () {

        var showSpeed = "300",
            showSpeedFast = showSpeed * 0.5,
            $formContainer = $('.signin-wrapper .form-container'),
            $searchWrapper = $('.search-wrapper'),
            $signinWrapper = $('.signin-wrapper'),
            toggleSignIn = function () {
                $('#signin-btn').toggleClass('selected');

                $('html, body').animate({scrollTop: $('body').offset().top}, 300, 'easeInOutQuad');

                if ($searchWrapper.hasClass('open-panel')) {
                    $('#search-btn').toggleClass('selected');
                    $searchWrapper.slideToggle(showSpeedFast, 'easeInOutQuad', function () {
                        $signinWrapper.slideToggle(showSpeed, 'easeInOutQuad', function () {
                            if ($('#password-form-container').hasClass('active-form')) {
                                $formContainer.toggleClass('active-form');
                                $formContainer.toggle();
                            }
                        }).toggleClass('open-panel');
                    }).toggleClass('open-panel');
                }
                else {
                    $signinWrapper.slideToggle(showSpeed, 'easeInOutQuad', function () {
                        if ($('#password-form-container').hasClass('active-form')) {
                            $formContainer.toggleClass('active-form');
                            $formContainer.toggle();
                        }
                    }).toggleClass('open-panel');
                }
            },
            getQueryStringValue = function (key) {
                return unescape(window.location.search.replace(new RegExp("^(?:.*[&\\?]" + escape(key).replace(/[\.\+\*]/g, "\\$&") + "(?:\\=([^&]*))?)?.*$", "i"), "$1"));
            },
            toggleSearch = function () {
                $('#search-btn').toggleClass('selected');
                if ($signinWrapper.hasClass('open-panel')) {
                    $('#signin-btn').toggleClass('selected');
                    $signinWrapper.slideToggle(showSpeed, 'easeInOutQuad', function () {
                        $searchWrapper.slideToggle(showSpeedFast, 'easeInOutQuad').toggleClass('open-panel');
                        if ($('#password-form-container').hasClass('active-form')) {
                            $formContainer.toggleClass('active-form');
                            $formContainer.toggle();
                        }
                    }).toggleClass('open-panel');
                } else {
                    $searchWrapper.slideToggle(showSpeedFast, 'easeInOutQuad').toggleClass('open-panel');
                }
            };

        if (getQueryStringValue('dl') === "account-settings" || getQueryStringValue('dl') === "account-subscription") {
            //alert('pasok');
            toggleSignIn();
        }

        // Sign In panel
        $('.signin-trigger').on('click', function () {
            toggleSignIn();
            return false;
        });

        // Inside the Sign In panel
        $('.form-toggle').on('click', function () {
            $formContainer.toggleClass('active-form');
            $formContainer.toggle();
            return false;
        });

        // Search panel
        $('.search-trigger').on('click', function () {
            toggleSearch();
            return false;
        });

        // Reset Sign In/Password Reset Forms
        $('.signin-wrapper .cancel-link').click(function () {
            $('.signin-wrapper form').each(function () {
                $('input', this).removeClass('error');
                $('label.error', this).css('display', 'none');
                this.reset();
            });
        });

        // Dropdowns in main nav
        $('ul.dropdown-menu [data-toggle=dropdown]').on('click', function (event) {
            // Avoid following the href location when clicking
            event.preventDefault();
            // Avoid having the menu close when clicking
            event.stopPropagation();
            // If a menu is already open, close it
            if ($(this).parent().hasClass('open')) {
                $(this).parent().removeClass('open');
            } else {
                // Opening the one you clicked on
                $(this).parent().siblings().removeClass('open');
                $(this).parent().addClass('open');
            }

            var menu = $(this).parent().find("ul"),
                menupos = menu.offset(),
                newpos = 0;

            if ((menupos.left + menu.width()) + 30 > $(window).width()) {
                newpos = -menu.width();
            } else {
                newpos = $(this).parent().width();
            }
            menu.css({left: newpos});

        });

        var signupbgcolor = 'rgba(48, 55, 56, 0.65)';
        var introVideobgColor = 'rgba(48, 55, 56, 0.65)';
        $('#signup-content').css("background-color", signupbgcolor);
        $('#intro-video-outer').css("background-color", introVideobgColor);

        $('[data-utc-date]').each(function () {
            var uDate = $(this).data('utc-date'),
                lDate = new Date(uDate);
            $(this).html((lDate.getMonth() + 1) + '/' + lDate.getDate() + '/' + lDate.getFullYear());
        });

        $('[data-utc-date-2]').each(function () {
            var uDate = $(this).data('utc-date-2'),
                lDate = new Date(uDate);
            $(this).html((lDate.getMonth() + 1) + '/' + lDate.getDate());
        });
    });

})(jQuery);