  $('[data-form-validate]').each(function() {
    // Card Number
    var
      $form = $(this),
      $cardNumber = $form.find('#card_number'),
      $cardZip = $form.find('#zip'),
      $cardDate = $form.find('#card_exp'),
      $cardCVV = $form.find('#card_cvv'),
      getNextInput = function ($el) {
        var idNextInput = $el.data('next-input'),
            $nextInput = $form.find(idNextInput),
            isHidden = $nextInput.is(':hidden');

        if (isHidden) {
          return getNextInput($nextInput);
        }

        return $nextInput;
      }
    ;

    $cardNumber.payment('formatCardNumber');

    $cardNumber.on('change keyup', function () {
      var $icoCards = $('.ico-cards'),
          valueInput = $(this).val(),
          cardType = $.payment.cardType(valueInput),
          isValidCard = $.payment.validateCardNumber(valueInput),
          $nextInput = getNextInput($(this));

      $icoCards.removeClass().addClass('ico ico-cards ico-card-' + cardType);

      if (isValidCard && $nextInput.length) {
        $nextInput.focus();
      }
    });

    $cardZip.on('change keyup', function () {
      var valueLength = $(this).val().length,
          isValid = parseInt($(this).attr('maxlength') || 5),
          $nextInput = getNextInput($(this));

      if (valueLength === isValid && $nextInput.length) {
        $nextInput.focus();
      }
    });

    $cardDate.on('change keyup', function () {
      var
        valueLength = $(this).val().length,
        isValid = parseInt($(this).attr('maxlength') || 5),
        $nextInput = getNextInput($(this))
      ;

      if (valueLength === isValid) {
        $(this).focusout();
      }

      var
        $formGroup = $(this).closest('.form-group'),
        $labelErr = $formGroup.find('.validate-error'),
        isValidDate = ($labelErr.length && $labelErr.is(':hidden') || !$labelErr.length)
      ;

      $(this).closest('[data-form-validate]')['form-validate']('handleValidate');

      if (valueLength === isValid && isValidDate && $nextInput.length) {
        $nextInput.focus();
      }
    });

    $cardCVV.on('change keyup', function () {
      var valueLength = $(this).val().length,
          isValid = parseInt($(this).attr('maxlength') || 4),
          $nextInput = getNextInput($(this));

      if (valueLength === isValid && $nextInput.length) {
        $nextInput.focus();
      }
    });
  });

