;(function ($, window, undefined) {
  'use strict';

  var pluginName = 'preferences',
    dataPreferences = {};

  function Plugin(element, options) {
    this.element = $(element);
    this.options = $.extend({}, $.fn[pluginName].defaults, this.element.data(), options);
    this.init();
  }

  Plugin.prototype = {
    init: function () {
      this.$inputPreferences = this.element.find(this.options.dataInputPreferences);
      this.$inputActive = null;

      this.getPreferenceData();
      this.searchTagEvent();
    },
    searchTagEvent: function () {
      var that = this;

      that.element.on('click', '.tagsinput', function () {
        var $input = $(this).find('input');

        that.$inputActive = $input;
        $input.autocomplete("search");
      });
    },

    initTagsInput: function () {
      var that = this;

      // Populate selected preference
      $.each(dataPreferences, function (prefName, prefData) {
        var $el = $(that.options.dataInputPreferences + '[data-preferences-name="' + prefName + '"]'),
          $autoWrapper = $el.closest('.content-group').find('.ui-autocomplete-wrapper'),
          _jScrollPane,
          _jScrollPaneAPI;

        $el.autocomplete({
          source: prefData.options,
        }).val(prefData.selected);

        $el.tagsInput({
          placeholder: that.options.tagsInputPlaceholder,
          autocomplete: {
            appendTo: $autoWrapper[0],
            delay: 500,
            source: prefData.options,
            minLength: 0,
            open: function () {
              if (undefined !== _jScrollPane) {
                _jScrollPaneAPI.destroy();
              }

              $autoWrapper.find('.ui-autocomplete > li')
                .wrapAll($('<div class="scroll-panel"></div>')
                  .css({
                    'max-height': 'calc(100vh - 400px)',
                    'width': $('.ui-autocomplete').width()
                  })
                );

              _jScrollPane = $autoWrapper.find('.scroll-panel').jScrollPane();
              _jScrollPaneAPI = _jScrollPane.data('jsp');
            },
            close: function (event, ui) {
              _jScrollPaneAPI.destroy();
              _jScrollPane = undefined;
            },
          },
          onAddTag: function (el, data) {
            that.updatePreferenceData($(el));
          },
          onRemoveTag: function (el, data) {
            that.updatePreferenceData($(el));
          }
        });
      });
    },
    getPreferenceData: function () {
      var that = this;

      $.fn.callAjaxGlobal({
        url: yahAjax.ajaxurl,
        method: 'GET',
        loader: true,
        data: {
          'action': 'yah_get_preferences'
        }
      }).done(function (res) {
        if (res && res.success) {
          dataPreferences = res.data.dataPreferences;
          that.initTagsInput();
        }
      }).fail(function (request, status, error) {
      });
    },
    updatePreferenceData: function ($el) {
      var that = this,
        preferencesName = $el.attr(that.options.dataPreferencesName),
        formCheck = $el.data(that.options.dataFormCheck);

      $.fn.callAjaxGlobal({
        url: yahAjax.ajaxurl,
        method: 'POST',
        data: {
          action: 'yah_update_preferences',
          preferencesName: preferencesName,
          data: $el.val(),
          formcheck: formCheck
        },
        loader: true
      }).done(function (res) {
      }).fail(function (request, status, error) {
      });
    },
    destroy: function () {
      $.removeData(this.element[0], pluginName);
    }
  };

  $.fn[pluginName] = function (options, params) {
    return this.each(function () {
      var instance = $.data(this, pluginName);
      if (!instance) {
        $.data(this, pluginName, new Plugin(this, options));
      } else if (instance[options]) {
        instance[options](params);
      }
    });
  };

  $.fn[pluginName].defaults = {
    tagsInputPlaceholder: 'None selected.',
    dataInputPreferences: '[data-suggest-preferences]',
    dataPreferencesName: 'data-preferences-name',
    dataFormCheck: 'formcheck'
  };

  $(function () {
    $('[data-' + pluginName + ']')[pluginName]();
  });

}(jQuery, window));
