;(function ($, window, undefined) {
  'use strict';

  var pluginName = 'upload-avatar';

  function Plugin(element, options) {
    this.element = $(element);
    this.options = $.extend({}, $.fn[pluginName].defaults, this.element.data(), options);
    this.init();
  }

  Plugin.prototype = {

    init: function () {
        this.cacheDOM();
        this.handleEvent();
    },

    cacheDOM: function () {
      this.input = this.element.find('input.file-input');
      this.avatarEl = $('body').find('img.avatar-img');
      this.$btnDeleteAvatar = this.element.find('.btn-delete-avatar');
      this.$btnConfirmDeleteAvatar = $(document).find('.del-avatar-confirm');
    },

    handleEvent: function () {
      this.onUploadAvatar();
      this.onConfirmDeleteAvatar();
    },

    onUploadAvatar: function () {
      var
        $that = this,
        $form = $that.element,
        $avatarEl = $that.avatarEl,
        $resultBlk = $form.find('.upload-avatar-result'),
        sizeLimit = $that.options.fileSizeLimit || 2097152,
        validTypes = ['image/gif', 'image/jpeg', 'image/png']
      ;

      $resultBlk.html('').addClass('d-none');

      $that.element.on('submit', function (e) {
        e.preventDefault();

        var
          upFile = $(this).find('input[type="file"]')[0].files[0],
          sizeInMb = Math.round(sizeLimit / 1024 / 1024)
        ;

        if (!validTypes.includes(upFile.type)) {
          $resultBlk.html('Please upload only image file.').removeClass('d-none');
          return;
        }

        if (upFile.size > sizeLimit) {
          $resultBlk.html('This profile image is too large to upload. Please use an image smaller than ' + sizeInMb + 'MB.').removeClass('d-none');
          return;
        }

        // create a loading bar and loading message
        var
          loading_bar = $('<div style=" width: 1%; height: 15px; background-color: #f18d7e; position: fixed; z-index: 10000; bottom: 0; "></div>'),
          loading_message = $('<div style=" width: 100%; height: 30px; text-align: right; position: fixed; z-index: 10001; bottom: 2em; "><h3 style="color: #f18d7e;">Uploading image...</h3></div>')
        ;

        // add our loading bar to the page
        $('body')
          .append(loading_bar)
          .append(loading_message);

        $resultBlk.html('Uploading image...').removeClass('d-none');

        $.ajax({
          url: yahAjax.ajaxurl,
          type: "POST",
          processData: false,
          contentType: false,
          data: new FormData(this),
          cache: false,
          success: function (res) {
            // delete loading bar
            loading_bar.remove();
            loading_message.remove();

            if (!res.success) {
              $resultBlk.html(res.data.message).removeClass('d-none');
            } else {
              //Update the image source
              if (typeof res.data.imgBase64 !== 'undefined' && res.data.imgBase64 !== '') {
                $avatarEl.attr("src", res.data.imgBase64);
                $resultBlk.html('').addClass('d-none');
                $that.$btnDeleteAvatar.css('display','block');
              }
            }
          },
          error: function (e) {
            // delete loading bar
            loading_bar.remove();
            loading_message.remove();

            $resultBlk.html('Unable to upload file.').removeClass('d-none');
          },
          xhr: function () {
            var xhr = new window.XMLHttpRequest();

            // update our loading bar on progress
            xhr.upload.addEventListener("progress", function (e) {
              if (e.lengthComputable) {
                var percent = e.loaded / e.total;
                loading_bar.css('width', (percent * 100) + '%');
                // console.log(percent);
              }
            }, false);

            return xhr;
          },
        });
      });

      this.input.on('change', function () {
        $form.submit();
      });
    },

    onConfirmDeleteAvatar: function () {
      var that = this;
      this.$btnConfirmDeleteAvatar
      .off('click')
      .on('click', function (e) {
        e.preventDefault();
        var target = $(e.target);

        $.fn.callAjaxGlobal({
          method  : "POST",
          url     : yahAjax.ajaxurl,
          dataType: 'json',
          data    : {
            "action"   : 'delete_avatar',
            "formcheck": that.$btnDeleteAvatar.attr('data-form-check'),
          },
          loader  : true
        }).done(function (res) {
          if (res.success) {
            $('.avatar-img').attr('src', that.$btnDeleteAvatar.attr('data-default-img'));
            $('.file-input').val('');
            that.$btnDeleteAvatar.css('display', 'none');
          }
        }).fail(function (request, status, error) {
          console.log('smth going wrong: ' + error);
        });
      });

    },

    destroy: function () {
      $.removeData(this.element[0], pluginName);
    }
  };

  $.fn[pluginName] = function (options, params) {
    return this.each(function () {
      var instance = $.data(this, pluginName);
      if (!instance) {
        $.data(this, pluginName, new Plugin(this, options));
      } else if (instance[options]) {
        instance[options](params);
      }
    });
  };

  $.fn[pluginName].defaults = {
    fileSizeLimit: 2097152, // 2mb
  };

  $(function () {
    $('[data-' + pluginName + ']')[pluginName]();
  });

}(jQuery, window));
